import {
  Box,
  Button,
  Divider,
  makeStyles,
  Typography,
} from '@material-ui/core';
import { useSnackbar } from 'notistack';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Moment from 'react-moment';
import { useAuth, useConfirmation } from '../../../hooks';
import useAPI, {
  getAPICallStatus,
  mergeAPIParams,
  useAPIPropTypes,
} from '../../../hooks/useAPI';
import i18n from '../../../i18n';
import Currency from '../Currency';
import Interval from '../Interval';
import PackageName from '../PackageName';
import PlanItems, {
  PlanItemsDefaultProps,
  PlanItemsPropTypes,
} from '../PlanItems';
import { en_en } from './CurrentOrder.lang.en-en';
import { nl_be } from './CurrentOrder.lang.nl-be';
import { nl_nl } from './CurrentOrder.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'CurrentOrder', en_en);
i18n.addResourceBundle('nl-NL', 'CurrentOrder', nl_nl);
i18n.addResourceBundle('nl-BE', 'CurrentOrder', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  subscriptionsCall: PropTypes.shape(useAPIPropTypes),
  planItems: PropTypes.shape(PlanItemsPropTypes),
  credits: PropTypes.string,
  creditResetDate: PropTypes.string,
  subscription: PropTypes.any,
  reloadSubscriptions: PropTypes.func,
};

/**
 * Defines the default props
 */
const defaultProps = {
  subscriptionsCall: {
    path: {
      endpoint: 'subscriptions',
    },
    watch: false,
  },
  planItems: PlanItemsDefaultProps,
  credits: '0',
  creditResetDate: '',
  subscription: {},
  realoadSubscription: () => {},
};

/**
 * Styles the component container
 */

const useStyles = makeStyles({
  listContainer: {
    paddingLeft: 20,
  },
  packageName: {
    fontWeight: 900,
  },
  interval: {
    textTransform: 'capitalize',
    textAlign: 'right',
  },
});

const today = new Date();

/**
 * Displays the component
 */
const CurrentOrder = (props) => {
  const {
    subscriptionsCall,
    planItems,
    credits,
    creditResetDate,
    subscription,
    reloadSubscriptions,
  } = props;
  const { listContainer, packageName, interval } = useStyles();
  const { t } = useTranslation('CurrentOrder');
  const { enqueueSnackbar } = useSnackbar();

  const { token } = useAuth();

  const [hasPlan, setHasPlan] = useState(true);
  /**
   * Checks if the user has active subscription
   * */

  useEffect(() => {
    if (!subscription) {
      setHasPlan(false);
    } else {
      setHasPlan(true);
    }
  }, [subscription]);

  /**
   * Handles the cancelation of a subscription
   */
  const [cancelApi, setCancelApi] = useState({});
  const { data: cancelData } = useAPI(cancelApi);

  useEffect(() => {
    const { successful, message } = getAPICallStatus(cancelData);
    if (successful) {
      enqueueSnackbar(t('subscription_canceled'), { variant: 'success' });
      reloadSubscriptions();
    } else if (message) {
      enqueueSnackbar(message, { variant: 'error' });
    }
  }, [cancelData]);

  const cancelSubscription = () => {
    const queryParams = {
      action: 'cancel',
      subscription: subscription.id,
      token: token,
    };

    setCancelApi(
      mergeAPIParams({
        requestProps: {
          ...subscriptionsCall,
          params: {
            queryParams: queryParams,
          },
        },
      })
    );
  };

  /**
   * Confirmation dialog state
   */

  const confirmation = useConfirmation();

  const openConfirmationDialog = () => {
    confirmation({
      onConfirm: cancelSubscription,
      title: t('dialog_title'),
      description: t('dialog_description'),
    });
  };

  return (
    <Box>
      <Box mb={1} display='flex' justifyContent='space-between'>
        <Box>
          <Typography variant='h5' color='primary' className={packageName}>
            <PackageName plan={subscription?.nickname || 'free'} />
          </Typography>
          <PlanItems
            {...planItems}
            className={listContainer}
            plan={subscription?.nickname}
            size='small'
            credits={subscription?.credits}
          />
        </Box>
        <Box>
          <Typography variant='h3'>
            <Currency currency={subscription?.currency} />
            &nbsp;
            {subscription?.amount || 0}
          </Typography>
          <Typography className={interval} variant='body2'>
            {t('per')}&nbsp;
            <Interval interval={subscription?.interval} />
          </Typography>
        </Box>
      </Box>
      <Divider></Divider>
      <Box mb={3} mt={3} display='flex' justifyContent='space-between'>
        <Typography variant='h5' color='primary'>
          {t('available_credit')}
        </Typography>
        <Typography variant='h5'>{credits}</Typography>
      </Box>
      <Box mb={3} display='flex' justifyContent='space-between'>
        <Typography variant='body2'>{t('reset_date')}</Typography>
        <Typography variant='body2'>
          <Moment format='DD/MM/YYYY'>{creditResetDate || today}</Moment>
        </Typography>
      </Box>
      {hasPlan && (
        <>
          <Divider></Divider>
          <Box mt={3} display='flex' justifyContent='flex-end'>
            <Button
              variant='contained'
              color='primary'
              onClick={openConfirmationDialog}
            >
              {t('cancel_subscription')}
            </Button>
          </Box>
        </>
      )}
    </Box>
  );
};

CurrentOrder.propTypes = propTypes;
CurrentOrder.defaultProps = defaultProps;

export default CurrentOrder;
export {
  propTypes as CurrentOrderPropTypes,
  defaultProps as CurrentOrderDefaultProps,
};
