import { Box, Collapse, Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useAPIPropTypes, useAuth, useQuery } from '../../../hooks';
import useAPI, {
  getAPICallStatus,
  mergeApiParams,
} from '../../../hooks/useAPI';
import i18n from '../../../i18n';
import ContentCard from '../../ContentCard';
import NavBar, { NavBarDefaultProps, NavBarPropTypes } from '../../NavBar';
import PageContent from '../../PageContent';
import Sidebar, { SidebarDefaultProps, SidebarPropTypes } from '../../Sidebar';
import HistoryTable, {
  HistoryTableDefaultProps,
  HistoryTablePropTypes,
} from '../HistoryTable';
import OverviewMessages, {
  OverviewMessagesDefaultProps,
  OverviewMessagesPropTypes,
} from '../OverviewMessages';
import { en_en } from './History.lang.en-en';
import { nl_be } from './History.lang.nl-be';
import { nl_nl } from './History.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'History', en_en);
i18n.addResourceBundle('nl-NL', 'History', nl_nl);
i18n.addResourceBundle('nl-BE', 'History', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  navbar: PropTypes.shape(NavBarPropTypes),
  sidebar: PropTypes.shape(SidebarPropTypes),
  historyTable: PropTypes.shape(HistoryTablePropTypes),
  overviewCall: PropTypes.shape(useAPIPropTypes),
  overviewMessages: PropTypes.shape(OverviewMessagesPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  navbar: NavBarDefaultProps,
  sidebar: SidebarDefaultProps,
  historyTable: HistoryTableDefaultProps,
  overviewCall: {
    path: {
      endpoint: 'overview',
    },
  },
  overviewMessages: OverviewMessagesDefaultProps,
};

/**
 * Styles the component container
 */

/**
 * Displays the component
 */
const History = (props) => {
  const {
    navbar,
    sidebar,
    historyTable,
    overviewCall,
    overviewMessages,
  } = props;
  const { isAuthenticated, token } = useAuth();
  const history = useHistory();

  /**
   * If the user is unauthenticated redirect to the homepage
   */
  if (!isAuthenticated) {
    history.push('/');
  }

  const { t } = useTranslation('History');

  const [historyData, setHistoryData] = useState([]);
  const [messages, setMessages] = useState([]);

  const query = useQuery();

  const message = query.get('message');

  /**
   * Fetches the history data and the overview messages
   */

  const { data } = useAPI(
    mergeApiParams({
      requestProps: {
        ...overviewCall,
        params: {
          queryParams: {
            token: token,
            message: message,
          },
        },
      },
    })
  );

  useEffect(() => {
    const { successful } = getAPICallStatus(data);

    if (successful) {
      setHistoryData(data.recent_searches);
      setMessages(data.overview.messages);
    }
  }, [data]);

  const [showMessages, setShowMessages] = useState(true);

  const handleMessageCancel = (message) => {
    setMessages((prevState) =>
      prevState.filter((item) => {
        return item.message !== message;
      })
    );

    if (messages.length === 1) {
      setShowMessages(false);
    }
  };

  return (
    <Box display='flex'>
      <NavBar {...navbar} position='fixed'></NavBar>
      <Sidebar {...sidebar} />
      <PageContent>
        <Grid container spacing={6}>
          <Grid item xs={12}>
            <Collapse in={showMessages}>
              <Box mb={6}>
                <OverviewMessages
                  {...overviewMessages}
                  messages={messages}
                  onCanceled={handleMessageCancel}
                />
              </Box>
            </Collapse>
            <ContentCard title={t('title')}>
              <HistoryTable {...historyTable} historyData={historyData} />
            </ContentCard>
          </Grid>
        </Grid>
      </PageContent>
    </Box>
  );
};

History.propTypes = propTypes;
History.defaultProps = defaultProps;

export default History;
export { propTypes as HistoryPropTypes, defaultProps as HistoryDefaultProps };
