import PropTypes from 'prop-types';
import queryString from 'query-string';
import React, { useEffect, useState } from 'react';
import Moment from 'react-moment';
import i18n from '../../../i18n';
import MaterialTable, {
  MaterialTableDefaultProps,
  MaterialTablePropTypes,
} from '../../MaterialTable';
import { FilterAgeDefaultProps } from '../../search/FilterAge';
import { en_en } from './HistoryTable.lang.en-en';
import { nl_be } from './HistoryTable.lang.nl-be';
import { nl_nl } from './HistoryTable.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'HistoryTable', en_en);
i18n.addResourceBundle('nl-NL', 'HistoryTable', nl_nl);
i18n.addResourceBundle('nl-BE', 'HistoryTable', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  materialTable: PropTypes.shape(MaterialTablePropTypes),
  historyData: PropTypes.any,
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      field: PropTypes.string,
    })
  ),
};

/**
 * Defines the default props
 */
const defaultProps = {
  materialTable: MaterialTableDefaultProps,
  historyData: [],
  columns: [
    {
      title: i18n.t('HistoryTable:country'),
      field: 'country',
    },
    {
      title: i18n.t('HistoryTable:industry'),
      field: 'category',
    },
    {
      title: i18n.t('HistoryTable:industry_code'),
      field: 'industryCode',
    },
    {
      title: i18n.t('HistoryTable:keywords'),
      field: 'text',
    },
    {
      title: i18n.t('HistoryTable:required_data'),
      field: 'required_data',
    },
    {
      title: i18n.t('HistoryTable:location'),
      field: 'location',
    },
    {
      title: i18n.t('HistoryTable:province'),
      field: 'province',
    },
    {
      title: i18n.t('HistoryTable:employees'),
      field: 'employees',
    },
    {
      title: i18n.t('HistoryTable:age'),
      field: 'age',
    },
    {
      title: i18n.t('HistoryTable:changed_since'),
      field: 'changedSince',
    },
    {
      title: i18n.t('HistoryTable:results'),
      field: 'found',
    },
  ],
};

const getQueryString = (query) => {
  return `http://api.finsterdata.com/v1/companies/?${queryString.stringify(
    query
  )}`;
};

const buildEmployeesString = (from, to) => {
  if (!from || !to) {
    return '-';
  }

  return `${from} : ${to}`;
};

const buildLocationString = (location, km) => {
  if (!location) {
    return '-';
  }

  if (km) {
    return `${location} ${km}`;
  } else {
    return location;
  }
};

const buildAgeString = (age) => {
  if (!age) {
    return '-';
  }

  const { values } = FilterAgeDefaultProps;

  const currentAge = values.find((ageItem) => {
    return String(ageItem.value) === age;
  });

  if (currentAge) {
    return currentAge.name;
  }

  return '-';
};

const formatDate = (date) => {
  if (!date) {
    return '-';
  }

  return <Moment format='DD/MM/YYYY'>{date}</Moment>;
};

const buildTableData = (data) => {
  return data.map((searchData) => {
    const queryString = getQueryString(searchData.query);
    const {
      country,
      category,
      text,
      required_data,
      location,
      from,
      to,
      km,
      age,
      province,
      industryCode,
      changedSince,
    } = searchData.query;
    return {
      country: country ?? '-',
      category: category ?? '-',
      text: text ?? '-',
      required_data: required_data ?? '-',
      location: buildLocationString(location, km),
      employees: buildEmployeesString(from, to),
      age: buildAgeString(age),
      province: province ?? '-',
      industryCode: industryCode ?? '-',
      changedSince: formatDate(changedSince),
      found: searchData.found,
      query: queryString,
    };
  });
};
/**
 * Displays the component
 */
const HistoryTable = (props) => {
  const { materialTable, historyData, columns } = props;
  const [rows, setRows] = useState([]);
  /**
   * Sets the content of the history table
   */
  useEffect(() => {
    setRows(buildTableData(historyData));
  }, [historyData]);

  const handleRowClicked = (_event, row) => {
    window.open(row.query);
  };

  return (
    <MaterialTable
      {...materialTable}
      columns={columns}
      data={rows}
      onRowClick={handleRowClicked}
      options={{
        search: false,
        paging: false,
        sorting: true,
        draggable: false,
        showTitle: false,
        toolbar: false,
      }}
    />
  );
};

HistoryTable.propTypes = propTypes;
HistoryTable.defaultProps = defaultProps;

export default HistoryTable;
export {
  propTypes as HistoryTablePropTypes,
  defaultProps as HistoryTableDefaultProps,
};
