import {
  Grid,
  makeStyles,
  Switch,
  Typography,
  useMediaQuery,
  useTheme,
} from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import shortid from 'shortid';
import i18n from '../../../i18n';
import Anchor from '../Anchor';
import Plan, { PlanDefaultProps, PlanPropTypes } from '../Plan';
import { en_en } from './Plans.lang.en-en';
import { nl_be } from './Plans.lang.nl-be';
import { nl_nl } from './Plans.lang.nl-nl';
i18n.addResourceBundle('en-EN', 'Plans', en_en);
i18n.addResourceBundle('nl-NL', 'Plans', nl_nl);
i18n.addResourceBundle('nl-BE', 'Plans', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  plans: PropTypes.arrayOf(PropTypes.shape(PlanPropTypes)),
};

/**
 * Defines the default props
 */
const defaultProps = {
  plans: [
    PlanDefaultProps,
    { ...PlanDefaultProps, highlight: true },
    PlanDefaultProps,
    { ...PlanDefaultProps, interval: 'month' },
    {
      ...PlanDefaultProps,
      plan: 'silver',
      interval: 'month',
      package: 'silver',
      highlight: true,
    },
    { ...PlanDefaultProps, interval: 'month', package: 'gold' },
  ],
};

/**
 * Styles the componet
 */

const useStyles = makeStyles((theme) => ({
  container: { position: 'relative' },
  switchContainer: {
    ...theme.mixins.center,
    marginBottom: theme.spacing(3),
  },
  center: {
    ...theme.mixins.center,
  },
  switchLabel: {
    fontWeight: 700,
  },
  monthLabel: (props) => ({
    opacity: props.interval === 'month' ? 1 : 0.4,
  }),
  yearLabel: (props) => ({
    opacity: props.interval === 'year' ? 1 : 0.4,
  }),
  plansContainer: {
    // marginTop: theme.spacing(5)
  },
  discountText: {
    marginBottom: theme.spacing(3),
  },
}));

/**
 * Displays the component
 */
const Plans = (props) => {
  const { plans } = props;
  const [interval, setInterval] = useState('month');
  const {
    center,
    container,
    switchContainer,
    switchLabel,
    monthLabel,
    yearLabel,
    plansContainer,
    discountText,
  } = useStyles({
    interval,
  });
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('lg'));
  const { t } = useTranslation('Plans');

  const handleSwitch = (event) => {
    const {
      target: { checked },
    } = event;
    setInterval(checked ? 'year' : 'month');
  };

  /**
   * Returns the plans of the selected interval (year or month)
   */
  const getCurrentPlans = (selectedInterval) => {
    return plans
      .filter((plan) => {
        const { interval } = plan;
        return interval === selectedInterval;
      })
      .map((plan) => {
        return (
          <Plan
            key={shortid.generate()}
            {...plan}
            highlight={plan.plan.includes('silver')}
          />
        );
      });
  };

  return (
    <Grid className={container} container alignItems='flex-end'>
      <Anchor id='plans' top={-100} />
      <Grid item xs={12}>
        <Grid item xs={12} className={switchContainer}>
          <Typography variant='body1' className={clsx(switchLabel, monthLabel)}>
            {t('monthly_plan', 'Monthly plan')}
          </Typography>
          <Switch onChange={handleSwitch}></Switch>
          <Typography variant='body1' className={clsx(switchLabel, yearLabel)}>
            {t('annual_plan', 'Annual plan')}
          </Typography>
        </Grid>
        {interval === 'year' && (
          <Grid item xs={12} className={center}>
            <Typography className={discountText} color='primary' variant='h3'>
              {t('discount_text', "Great! You're saving over 15%!")}
            </Typography>
          </Grid>
        )}
      </Grid>
      <Grid
        container
        className={plansContainer}
        spacing={matches ? 6 : 8}
        justify='flex-end'
        alignItems='center'
      >
        {getCurrentPlans(interval)}
      </Grid>
    </Grid>
  );
};

Plans.propTypes = propTypes;
Plans.defaultProps = defaultProps;

export default Plans;
export { propTypes as PlansPropTypes, defaultProps as PlansDefaultProps };
