import { Box, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import { useTranslation } from 'react-i18next';
import i18n from '../../../i18n';
import { en_en } from './AuthFooter.lang.en-en';
import { nl_be } from './AuthFooter.lang.nl-be';
import { nl_nl } from './AuthFooter.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'AuthFooter', en_en);
i18n.addResourceBundle('nl-NL', 'AuthFooter', nl_nl);
i18n.addResourceBundle('nl-BE', 'AuthFooter', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {};

/**
 * Defines the default props
 */
const defaultProps = {};

/**
 * Styles the component container
 */

const useStyles = makeStyles({
  footer: {
    marginTop: 'auto',
  },
  footerText: {
    fontSize: '1.125rem',
  },
});

/**
 * Displays the component
 */
const AuthFooter = (props) => {
  const { t } = useTranslation('AuthFooter');
  const { footer, footerText } = useStyles();
  return (
    <Box className={footer}>
      <Typography className={footerText}>{t('copyright')}</Typography>
    </Box>
  );
};

AuthFooter.propTypes = propTypes;
AuthFooter.defaultProps = defaultProps;

export default AuthFooter;
export {
  propTypes as AuthFooterPropTypes,
  defaultProps as AuthFooterDefaultProps,
};
