import { Box, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { ReactComponent as PrinterIcon } from '../../../assets/printer.svg';
/**
 * TODO: relocate assets to ensure SRP
 */
import { ReactComponent as SettingsIcon } from '../../../assets/settings-1.svg';
import { ReactComponent as UploadIcon } from '../../../assets/upload.svg';
import i18n from '../../../i18n';
import { en_en } from './FeatureIcon.lang.en-en';
import { nl_be } from './FeatureIcon.lang.nl-be';
import { nl_nl } from './FeatureIcon.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'FeatureIcon', en_en);
i18n.addResourceBundle('nl-NL', 'FeatureIcon', nl_nl);
i18n.addResourceBundle('nl-BE', 'FeatureIcon', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  icon: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  icon: '',
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  iconContainer: {
    width: 90,
    height: 90,
    borderRadius: '50%',
    border: `2px solid ${theme.palette.primary.main}`,
    ...theme.mixins.center,
  },
}));

/**
 * Returns the icon
 */
const getIcon = (icon) => {
  switch (icon) {
    case 'settings':
      return <SettingsIcon />;
    case 'upload':
      return <UploadIcon />;
    case 'printer':
      return <PrinterIcon />;
    default:
      return null;
  }
};
/**
 * Displays the component
 */
const FeatureIcon = (props) => {
  const { icon } = props;
  const { iconContainer } = useStyles();
  return <Box className={iconContainer}>{getIcon(icon)}</Box>;
};

FeatureIcon.propTypes = propTypes;
FeatureIcon.defaultProps = defaultProps;

export default FeatureIcon;
export {
  propTypes as FeatureIconPropTypes,
  defaultProps as FeatureIconDefaultProps,
};
