import { Box, Button, Grid, makeStyles } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';
import React, { useEffect, useImperativeHandle, useRef } from 'react';
import { Controller, useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import useInput from '../../../hooks/useInput/useInput';
import i18n from '../../../i18n';
import Input from '../../Input';
import { en_en } from './AccountForm.lang.en-en';
import { nl_be } from './AccountForm.lang.nl-be';
import { nl_nl } from './AccountForm.lang.nl-nl';
import * as yup from 'yup';

i18n.addResourceBundle('en-EN', 'AccountForm', en_en);
i18n.addResourceBundle('nl-NL', 'AccountForm', nl_nl);
i18n.addResourceBundle('nl-BE', 'AccountForm', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    country_code: PropTypes.string,
    company_name: PropTypes.string,
    address: PropTypes.string,
    address2: PropTypes.string,
    postcode: PropTypes.string,
    city: PropTypes.string,
    vat: PropTypes.string,
  }),
  onSubmit: PropTypes.func,
  error: PropTypes.string,
  disableButton: PropTypes.func,
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    country_code: '',
    company_name: '',
    address: '',
    address2: '',
    postcode: '',
    city: '',
    vat: '',
  },
  onSubmit: (event) => {
    console.log('Account form submitted', event);
  },
  error: null,
  disableButton: () => {},
};
/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
  },
}));
/**
 * Displays the component
 */
const AccountForm = React.forwardRef((props, ref) => {
  const { defaultValues, onSubmit, error, disableButton } = props;
  const { form } = useStyles();
  const { t } = useTranslation('AccountForm');

  const schema = yup.object().shape({
    company_name: yup.string().required(t('required')),
    address: yup.string().required(t('required')),
    city: yup.string().required(t('required')),
    country_code: yup.string().required(t('required')),
    postcode: yup.string().required(t('required')),
  });
  const { parseDefaultInputs } = useInput();
  const inputRef = useRef();
  const formRef = useRef(null);
  const { control, formState, handleSubmit, errors, reset } = useForm({
    defaultValues,
    validationSchema: schema,
  });

  useEffect(() => {
    reset(parseDefaultInputs(defaultValues));
  }, [defaultValues, reset]);

  useEffect(() => {
    if (!formState.isValid && formState.isSubmitted) {
      disableButton(true);
    } else {
      disableButton(false);
    }
  }, [formState]);

  useImperativeHandle(ref, () => ({
    focus: () => {
      inputRef.current.focus();
    },
    form: formRef.current,
  }));

  return (
    <Box width={1}>
      <form ref={formRef} onSubmit={handleSubmit(onSubmit)} className={form}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Controller
              as={Input}
              name='company_name'
              control={control}
              type='text'
              fullWidth
              inputRef={inputRef}
              inputText={{
                required: true,
                error: errors.company_name?.message,
                label: t('company_name'),
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <Controller
              as={Input}
              type='text'
              name='address'
              control={control}
              fullWidth
              inputText={{
                required: true,
                error: errors.address?.message,
                label: t('address'),
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <Controller
              as={Input}
              type='text'
              control={control}
              fullWidth
              name='address2'
              inputText={{
                required: false,
                label: t('address2'),
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <Controller
              as={Input}
              type='text'
              control={control}
              fullWidth
              name='city'
              inputText={{
                required: true,
                error: errors.city?.message,
                label: t('city'),
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <Controller
              as={Input}
              type='text'
              control={control}
              fullWidth
              name='country_code'
              inputText={{
                required: true,
                error: errors.country_code?.message,
                label: t('country_code'),
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <Controller
              as={Input}
              type='text'
              control={control}
              fullWidth
              name='postcode'
              inputText={{
                required: true,
                error: errors.postcode?.message,
                label: t('post_code'),
              }}
            />
          </Grid>
          <Grid item xs={6}>
            <Controller
              as={Input}
              type='text'
              control={control}
              fullWidth
              name='vat'
              inputText={{
                label: t('vat'),
              }}
            />
          </Grid>
          {error && (
            <Grid item xs={12}>
              <Alert variant='filled' severity='error'>
                {error}
              </Alert>
            </Grid>
          )}
        </Grid>
        <Button type='submit' style={{ visibility: 'hidden' }}>
          ''
        </Button>
      </form>
    </Box>
  );
});

AccountForm.propTypes = propTypes;
AccountForm.defaultProps = defaultProps;

export default AccountForm;
export {
  propTypes as AccountFormPropTypes,
  defaultProps as AccountFormDefaultProps,
};
