import {
  Box,
  Button,
  Grid,
  makeStyles,
  Typography,
  Link,
} from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';
import React from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink } from 'react-router-dom';
import i18n from '../../../i18n';
import Input from '../../Input';
import { en_en } from './LoginForm.lang.en-en';
import { nl_be } from './LoginForm.lang.nl-be';
import { nl_nl } from './LoginForm.lang.nl-nl';
import * as yup from 'yup';

i18n.addResourceBundle('en-EN', 'LoginForm', en_en);
i18n.addResourceBundle('nl-NL', 'LoginForm', nl_nl);
i18n.addResourceBundle('nl-BE', 'LoginForm', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    email: PropTypes.string,
    password: PropTypes.string,
    rememberMe: PropTypes.bool,
  }),
  onSubmit: PropTypes.func,
  error: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    email: '',
    password: '',
    rememberMe: false,
  },
  onSubmit: () => {},
  error: null,
};

/**
 * Displays the component
 */
const LoginForm = (props) => {
  const { defaultValues, onSubmit, error } = props;
  const { t } = useTranslation('LoginForm');

  /**
   * TODO: move validation texts into a separate hook? Ex. useValidationText('email_invalid')
   */
  const schema = yup.object().shape({
    email: yup.string().required(t('email_required')).email(t('email_invalid')),
    password: yup
      .string()
      .required(t('password_required'))
      .min(6, t('password_min_length')),
  });

  const { register, errors, reset, handleSubmit, formState } = useForm({
    defaultValues,
    validationSchema: schema,
  });

  const onFormSubmit = (...args) => {
    reset();
    onSubmit(...args);
  };

  return (
    <Box width={1}>
      <form onSubmit={handleSubmit(onFormSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Input
              type='email'
              inputRef={register}
              fullWidth
              inputEmail={{
                name: 'email',
                label: t('email'),
                error: errors.email?.message,
                required: true,
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <Input
              fullWidth
              type='password'
              inputRef={register}
              inputPassword={{
                name: 'password',
                label: t('password'),
                error: errors.password?.message,
                required: true,
              }}
            />
          </Grid>

          <Grid item xs={12}>
            <Input
              type='checkbox'
              inputRef={register}
              inputCheckbox={{
                name: 'rememberMe',
                label: t('rememberMe'),
              }}
            />
          </Grid>

          {error && !formState.dirty && (
            <Grid item xs={12}>
              <Alert variant='filled' severity='error'>
                {error}
              </Alert>
            </Grid>
          )}
          <Grid item xs={12}>
            <Button
              disabled={!formState.isValid && formState.isSubmitted}
              variant='contained'
              color='primary'
              type='submit'
              fullWidth
            >
              {t('submit')}
            </Button>
          </Grid>
        </Grid>
      </form>
      <Grid container spacing={2}>
        <Grid item xs>
          <Typography variant='body2'>
            <Link component={RouterLink} to={'/register'}>
              {t('toRegister', 'Forgot password?')}
            </Link>
          </Typography>
        </Grid>
        <Grid item>
          <Typography variant='body2'>
            <Link component={RouterLink} to={'/register'}>
              {t('toRegister', "Don't have an account? Register")}
            </Link>
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant='body2'>
            <Link component={RouterLink} to={'/'}>
              {t('toHome', 'Back To Home')}
            </Link>
          </Typography>
        </Grid>
      </Grid>
    </Box>
  );
};

LoginForm.propTypes = propTypes;
LoginForm.defaultProps = defaultProps;

export default LoginForm;
export {
  propTypes as LoginFormPropTypes,
  defaultProps as LoginFormDefaultProps,
};
