import { Box, Link, makeStyles, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import shortid from 'shortid';
import i18n from '../../../i18n';
import { en_en } from './FooterLinkList.lang.en-en';
import { nl_be } from './FooterLinkList.lang.nl-be';
import { nl_nl } from './FooterLinkList.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'FooterLinkList', en_en);
i18n.addResourceBundle('nl-NL', 'FooterLinkList', nl_nl);
i18n.addResourceBundle('nl-BE', 'FooterLinkList', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  title: PropTypes.string,
  links: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      to: PropTypes.string,
      isReactLink: PropTypes.bool,
    })
  ),
};

/**
 * Defines the default props
 */
const defaultProps = {
  title: null,
  links: [],
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  linkGroup: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
    height: '100%',

    '& .MuiLink-root:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },

    '& .MuiLink-root:nth-of-type(1)': {
      marginTop: 'auto',
    },
  },
  groupTitle: {
    marginBottom: theme.spacing(2),
    color: theme.palette.text.white,
    // [theme.breakpoints.down('sm')]: {
    //   marginBottom: 'auto'
    // }
  },
}));

/**
 * Returns a list of links
 */
const renderLinks = (props) => {
  const { links } = props;

  return links.map((link) => {
    const { text, to, isReactLink } = link;
    return (
      <Link
        key={shortid.generate()}
        {...(isReactLink && { component: RouterLink })}
        to={to}
        href={isReactLink ? null : to}
        color='inherit'
        variant='body1'
      >
        {text}
      </Link>
    );
  });
};

/**
 * Displays the component
 */
const FooterLinkList = (props) => {
  const { title } = props;
  const { linkGroup, groupTitle } = useStyles();

  return (
    <Box className={linkGroup}>
      {title && (
        <Typography className={groupTitle} variant='h5' color='textSecondary'>
          {title}
        </Typography>
      )}
      {renderLinks(props)}
    </Box>
  );
};

FooterLinkList.propTypes = propTypes;
FooterLinkList.defaultProps = defaultProps;

export default FooterLinkList;
export {
  propTypes as FooterLinkListPropTypes,
  defaultProps as FooterLinkListDefaultProps,
};
