import { Box, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { ReactComponent as Facebook } from '../../../assets/facebook.svg';
import { ReactComponent as Google } from '../../../assets/google.svg';
import { ReactComponent as Instagram } from '../../../assets/instagram.svg';
import { ReactComponent as Linkedin } from '../../../assets/linkedin.svg';
import i18n from '../../../i18n';
import { en_en } from './SocialMediaIcon.lang.en-en';
import { nl_be } from './SocialMediaIcon.lang.nl-be';
import { nl_nl } from './SocialMediaIcon.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'SocialMediaIcon', en_en);
i18n.addResourceBundle('nl-NL', 'SocialMediaIcon', nl_nl);
i18n.addResourceBundle('nl-BE', 'SocialMediaIcon', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  icon: PropTypes.oneOf([
    'facebook',
    'google',
    'instagram',
    'linkedin',
    'default',
  ]),
  size: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
};

/**
 * Defines the default props
 */
const defaultProps = {
  icon: 'default',
  size: 44,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  iconContainer: (props) => ({
    border: `1px solid ${theme.palette.primary.main}`,
    borderRadius: '50%',
    width: props.size,
    height: props.size,
    ...theme.mixins.center,
  }),
}));

/**
 * Returns the corresponding icon
 */
const getIcon = (icon) => {
  switch (icon) {
    case 'facebook':
      return <Facebook />;
    case 'google':
      return <Google />;
    case 'instagram':
      return <Instagram />;
    case 'linkedin':
      return <Linkedin />;
    default:
      return null;
  }
};

/**
 * Displays the component
 */
const SocialMediaIcon = (props) => {
  const { icon } = props;
  const { iconContainer } = useStyles(props);
  return <Box className={iconContainer}>{getIcon(icon)}</Box>;
};

SocialMediaIcon.propTypes = propTypes;
SocialMediaIcon.defaultProps = defaultProps;

export default SocialMediaIcon;
export {
  propTypes as SocialMediaIconPropTypes,
  defaultProps as SocialMediaIconDefaultProps,
};
