import { Box, Button, Grid, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import i18n from '../../../i18n';
import Input from '../../Input';
import { en_en } from './ContactForm.lang.en-en';
import { nl_be } from './ContactForm.lang.nl-be';
import { nl_nl } from './ContactForm.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'ContactForm', en_en);
i18n.addResourceBundle('nl-NL', 'ContactForm', nl_nl);
i18n.addResourceBundle('nl-BE', 'ContactForm', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    name: PropTypes.string,
    email: PropTypes.string,
    description: PropTypes.string,
  }),
  onSubmit: PropTypes.func,
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    name: '',
    email: '',
    description: '',
  },
  onSubmit: () => console.log('ContactForm on submit'),
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
  },
}));
/**
 * Displays the component
 */
const ContactForm = (props) => {
  const { defaultValues, onSubmit } = props;
  const { form } = useStyles();
  const { register, handleSubmit, formState } = useForm({
    defaultValues,
  });
  const { t } = useTranslation('ContactForm');
  return (
    <Box width={1}>
      <form className={form}>
        <Grid container spacing={3}>
          <Grid item xs={12} lg={6}>
            <Input
              type='text'
              fullWidth
              inputRef={register()}
              inputText={{
                name: 'name',
                label: t('name'),
                required: true,
              }}
            />
          </Grid>
          <Grid item xs={12} lg={6}>
            <Input
              type='email'
              fullWidth
              inputRef={register}
              inputEmail={{
                name: 'email',
                label: t('email'),
                required: true,
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <Input
              type='textArea'
              fullWidth
              inputRef={register()}
              inputTextArea={{
                name: 'description',
                label: t('description'),
                required: true,
              }}
            />
          </Grid>
          <Grid item xs={12} lg>
            <Button
              disabled={!formState.isValid && formState.isSubmitted}
              variant='contained'
              color='primary'
              onClick={handleSubmit(onSubmit)}
            >
              {t('submit')}
            </Button>
          </Grid>
        </Grid>
      </form>
    </Box>
  );
};

ContactForm.propTypes = propTypes;
ContactForm.defaultProps = defaultProps;

export default ContactForm;
export {
  propTypes as ContactFormPropTypes,
  defaultProps as ContactFormDefaultProps,
};
