import { Button } from '@material-ui/core';
import { useStripe } from '@stripe/react-stripe-js';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useAPI, useAPIPropTypes, useAuth } from '../../../hooks';
import { getAPICallStatus, mergeAPIParams } from '../../../hooks/useAPI';
import i18n from '../../../i18n';
import ContentCard from '../../ContentCard';
import CreditForm, {
  CreditFormDefaultProps,
  CreditFormPropTypes,
} from '../CreditForm';
import { en_en } from './CreditsCard.lang.en-en';
import { nl_be } from './CreditsCard.lang.nl-be';
import { nl_nl } from './CreditsCard.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'CreditsCard', en_en);
i18n.addResourceBundle('nl-NL', 'CreditsCard', nl_nl);
i18n.addResourceBundle('nl-BE', 'CreditsCard', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  creditForm: PropTypes.shape(CreditFormPropTypes),
  orderCall: PropTypes.shape(useAPIPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  creditForm: CreditFormDefaultProps,
  orderCall: {
    path: {
      endpoint: 'order',
    },
  },
};

/**
 * Displays the component
 */
const CreditsCard = (props) => {
  const { creditForm, orderCall } = props;
  const { t } = useTranslation('CreditsCard');
  const { token } = useAuth();
  const [orderButtonDisabled, setOrderButtonDisabled] = useState(false);

  const creditFormRef = useRef(null);

  const [orderApi, setOrderApi] = useState({});

  const { data } = useAPI(orderApi);
  /**
   * Submits the credit form
   */
  const submitCreditForm = () => {
    creditFormRef.current.dispatchEvent(new Event('submit'));
  };

  /**
   * Disables/enables the submit button of the order form
   */
  const disableOrderSubmitButton = (state) => {
    if (state !== orderButtonDisabled) {
      setOrderButtonDisabled(state);
    }
  };

  /**
   * Handles the credit form submit
   */
  const buyCredits = (form) => {
    setOrderApi(
      mergeAPIParams({
        requestProps: {
          ...orderCall,
          params: {
            queryParams: {
              token: token,
              // form: 1,
              // example: 1,
              extra_credits: form.credits,
              success_url:
                'http://localhost:3000/history?message=payment_succes',
              cancel_url:
                'http://localhost:3000/history?message=payment_cancel',
            },
          },
        },
      })
    );
  };

  /**
   * Handle stripe
   */
  const stripe = useStripe();

  useEffect(() => {
    const { successful } = getAPICallStatus(data);

    if (successful) {
      stripe.redirectToCheckout({
        sessionId: data.stripe_sessionid,
      });
    }
  }, [data]);

  return (
    <>
      <ContentCard
        title={t('add_credits')}
        actionComponent={
          <Button
            variant='contained'
            color='primary'
            disabled={orderButtonDisabled}
            onClick={submitCreditForm}
          >
            {t('buy')}
          </Button>
        }
      >
        <CreditForm
          ref={creditFormRef}
          {...creditForm}
          onSubmit={buyCredits}
          disableButton={disableOrderSubmitButton}
        />
      </ContentCard>
    </>
  );
};

CreditsCard.propTypes = propTypes;
CreditsCard.defaultProps = defaultProps;

export default CreditsCard;
export {
  propTypes as CreditsCardPropTypes,
  defaultProps as CreditsCardDefaultProps,
};
