import { Box } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import shortid from 'shortid';
import i18n from '../../../i18n';
import SocialMediaIcon, {
  SocialMediaIconDefaultProps,
  SocialMediaIconPropTypes,
} from '../SocialMediaIcon/SocialMediaIcon';
import { en_en } from './SocialMediaIconList.lang.en-en';
import { nl_be } from './SocialMediaIconList.lang.nl-be';
import { nl_nl } from './SocialMediaIconList.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'SocialMediaIconList', en_en);
i18n.addResourceBundle('nl-NL', 'SocialMediaIconList', nl_nl);
i18n.addResourceBundle('nl-BE', 'SocialMediaIconList', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  icons: PropTypes.arrayOf(SocialMediaIconPropTypes.icon),
  socialMediaIcon: PropTypes.shape(SocialMediaIconPropTypes),
  spacing: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

/**
 * Defines the default props
 */
const defaultProps = {
  icons: ['facebook', 'google', 'linkedin', 'instagram'],
  socialMediaIcon: SocialMediaIconDefaultProps,
  spacing: 24,
};

/**
 * Styles the component container
 */

/**
 * Displays the component
 */
const SocialMediaIconList = (props) => {
  const { icons, socialMediaIcon, spacing } = props;
  return (
    <Box display='flex'>
      {icons.map((icon) => {
        return (
          <Box mr={spacing} key={shortid.generate()}>
            <SocialMediaIcon {...socialMediaIcon} icon={icon} />
          </Box>
        );
      })}
    </Box>
  );
};

SocialMediaIconList.propTypes = propTypes;
SocialMediaIconList.defaultProps = defaultProps;

export default SocialMediaIconList;
export {
  propTypes as SocialMediaIconListPropTypes,
  defaultProps as SocialMediaIconListDefaultProps,
};
