import { makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import shortid from 'shortid';
import i18n from '../../../i18n';
import { en_en } from './PlanItems.lang.en-en';
import { nl_be } from './PlanItems.lang.nl-be';
import { nl_nl } from './PlanItems.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'PlanItems', en_en);
i18n.addResourceBundle('nl-NL', 'PlanItems', nl_nl);
i18n.addResourceBundle('nl-BE', 'PlanItems', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  plan: PropTypes.string,
  credits: PropTypes.string,
  className: PropTypes.any,
  size: PropTypes.oneOf(['small', 'large']),
};

/**
 * Defines the default props
 */
const defaultProps = {
  plan: null,
  credits: '0',
  className: null,
  size: 'large',
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  list: (props) => ({
    listStyle: 'none',
    '& li': {
      position: 'relative',
      marginLeft: theme.spacing(1),
    },
    '& li:before': {
      content: '""',
      backgroundColor: theme.palette.primary.main,
      width: props.size === 'large' ? 8 : 6,
      height: props.size === 'large' ? 8 : 6,
      borderRadius: '50%',
      fontWeight: 'bold',
      display: 'inline-block',
      marginRight: theme.spacing(3),
      marginLeft: -theme.spacing(3),
      position: 'absolute',
      ...theme.mixins.centerVerticalAbsolute,
    },
  }),
  listItem: {
    marginBottom: theme.spacing(3),
  },
}));

/**
 * Displays the component
 */
const PlanItems = (props) => {
  const { plan, credits, className, size } = props;
  const { list, listItem } = useStyles(props);
  const { t } = useTranslation('PlanItems');
  const formatPlanName = (plan) => {
    if (!plan) {
      return '';
    }
    if (plan.includes('bronze')) {
      return 'bronze';
    } else if (plan.includes('silver')) {
      return 'silver';
    } else if (plan.includes('gold')) {
      return 'gold';
    } else {
      return '';
    }
  };

  const getPlanItems = (plan) => {
    const credit = t('credits');
    const user = t('user');
    const users = t('users');
    const api = t('api');

    switch (formatPlanName(plan)) {
      case 'bronze':
        return [`${credits} ${credit}`, `1 ${user}`];
      case 'silver':
        return [`${credits} ${credit}`, `2 ${users}`];
      case 'gold':
        return [`${credits} ${credit}`, `4 ${users}`, api];
      default:
        return [];
    }
  };

  const getSize = (size) => {
    switch (size) {
      case 'small':
        return 'body2';
      case 'large':
        return 'body1';
      default:
        return 'body2';
    }
  };

  const renderPlanItems = () => {
    const items = getPlanItems(plan);
    return items.map((item) => {
      return (
        <Typography
          key={shortid.generate()}
          component='li'
          variant={getSize(size)}
          align='left'
          className={listItem}
        >
          {item}
        </Typography>
      );
    });
  };

  return <ul className={clsx(className, list)}>{renderPlanItems()}</ul>;
};

PlanItems.propTypes = propTypes;
PlanItems.defaultProps = defaultProps;

export default PlanItems;
export {
  propTypes as PlanItemsPropTypes,
  defaultProps as PlanItemsDefaultProps,
};
