import { Box, Button, Grid } from '@material-ui/core';
import { useStripe } from '@stripe/react-stripe-js';
import { isEqual } from 'lodash-es';
import { useSnackbar } from 'notistack';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory } from 'react-router-dom';
import { useAuth } from '../../../hooks';
import useAPI, {
  getAPICallStatus,
  mergeAPIParams,
  useAPIPropTypes,
} from '../../../hooks/useAPI';
import useQuery from '../../../hooks/useQuery';
import i18n from '../../../i18n';
import ContentCard from '../../ContentCard';
import NavBar, { NavBarDefaultProps, NavBarPropTypes } from '../../NavBar';
import PageContent from '../../PageContent';
import Sidebar, { SidebarDefaultProps, SidebarPropTypes } from '../../Sidebar';
import AccountForm, {
  AccountFormDefaultProps,
  AccountFormPropTypes,
} from '../AccountForm';
import CreditsCard from '../CreditsCard';
import CurrentOrder, {
  CurrentOrderDefaultProps,
  CurrentOrderPropTypes,
} from '../CurrentOrder';
import { en_en } from './Account.lang.en-en';
import { nl_be } from './Account.lang.nl-be';
import { nl_nl } from './Account.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Account', en_en);
i18n.addResourceBundle('nl-NL', 'Account', nl_nl);
i18n.addResourceBundle('nl-BE', 'Account', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  navBar: PropTypes.shape(NavBarPropTypes),
  accountForm: PropTypes.shape(AccountFormPropTypes),
  sidebar: PropTypes.shape(SidebarPropTypes),
  accountCall: PropTypes.shape(useAPIPropTypes),
  subscriptionsCall: PropTypes.shape(useAPIPropTypes),
  orderPlanCall: PropTypes.shape(useAPIPropTypes),
  currentOrder: PropTypes.shape(CurrentOrderPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  navBar: NavBarDefaultProps,
  accountForm: AccountFormDefaultProps,
  sidebar: SidebarDefaultProps,
  accountCall: {
    path: {
      endpoint: 'account',
    },
    watch: false,
  },
  orderPlanCall: {
    path: {
      endpoint: 'order',
    },
  },
  subscriptionsCall: {
    path: {
      endpoint: 'subscriptions',
    },
  },
  currentOrder: CurrentOrderDefaultProps,
};

/**
 * Styles the component container
 */

const plans = ['bronze', 'silver', 'gold'];
/**
 * Checks if it's a payment plan
 */
const isPaymentPlan = (plan) => {
  return plans.some((p) => {
    return plan && plan.includes(p);
  });
};

/**
 * Displays the component
 */
const Account = (props) => {
  const {
    accountForm,
    sidebar,
    navBar,
    accountCall,
    currentOrder,
    orderPlanCall,
    subscriptionsCall,
  } = props;
  const { isAuthenticated, token } = useAuth();
  const history = useHistory();

  /**
   * If the user is unauthenticated redirect to the homepage
   */
  if (!isAuthenticated) {
    history.push('/');
  }

  const { t } = useTranslation('Account');

  const [defaultFormValues, setDefaultFormValues] = useState(
    accountForm.defaultValues
  );
  const [formError, setFormError] = useState(null);

  const [accountButtonDisabled, setAccountButtonDisabled] = useState(false);

  const [subscription, setSubscription] = useState(null);

  const query = useQuery();

  const { enqueueSnackbar } = useSnackbar();

  const accountFormRef = useRef(null);

  const stripe = useStripe();

  const plan = query.get('plan');

  const isSamePlan = subscription?.plan === plan;

  /**
   * Handle mount
   */
  useEffect(() => {
    if (isPaymentPlan(plan) && accountFormRef.current) {
      accountFormRef.current.focus();
    }
  }, [accountFormRef, isPaymentPlan]);

  /**
   * Validate VAT request
   */

  const [vatApi, setVatApi] = useState({});

  useAPI(vatApi);

  /**
   * Order a Plan request
   */

  const [orderPlanApi, setOrderPlanApi] = useState({});

  const { data: orderPlanData } = useAPI(orderPlanApi);

  useEffect(() => {
    const { successful, message } = getAPICallStatus(orderPlanData);

    if (successful) {
      setFormError(null);
      stripe.redirectToCheckout({
        sessionId: orderPlanData.stripe_sessionid,
      });
    } else {
      setFormError(message);
    }
  }, [orderPlanData]);

  /**
   * GET Account request
   */
  const { data: accountData } = useAPI(
    mergeAPIParams({
      requestProps: {
        ...accountCall,
        params: {
          queryParams: {
            token: token,
          },
        },
      },
    })
  );

  useEffect(() => {
    const { successful } = getAPICallStatus(accountData);

    if (successful) {
      setDefaultFormValues(accountData.account);
    }
  }, [accountData]);

  /**
   * Update Account request
   */

  const [updateApi, setUpdateApi] = useState({});

  const { data: updateAccountData } = useAPI(updateApi);

  useEffect(() => {
    const { successful, message } = getAPICallStatus(updateAccountData);

    if (successful) {
      enqueueSnackbar(t('account_updated'), { variant: 'success' });
      setFormError(null);
    } else {
      setFormError(message);
    }
  }, [updateAccountData]);

  /**
   * Submits the account form
   */
  const submitAccountForm = () => {
    accountFormRef.current.form.dispatchEvent(new Event('submit'));
  };

  /**
   * Checks if the provided VAT is valid. If yes, updates the account
   **/
  const checkVat = (account) => {
    const { country_code, vat } = account;

    if (vat) {
      const queryParams = {
        country: country_code,
        vat: vat,
        token: token,
      };
      setVatApi(
        mergeAPIParams({
          requestProps: {
            path: {
              endpoint: 'vat',
            },
            params: {
              queryParams: queryParams,
            },
            watch: queryParams,
            config: {
              onResolve: (data) => checkIfShouldUpdateAccount(account, data),
            },
          },
        })
      );
    } else {
      updateAccount(account);
    }
  };

  /**
   * Calls the update Account request if the VAT is valid
   */
  const checkIfShouldUpdateAccount = (account, vatData) => {
    const { successful } = getAPICallStatus(vatData);
    if (successful) {
      setFormError(null);
      updateAccount(account);
    } else {
      setFormError(t('invalid_vat'));
    }
  };

  /**
   * Returns the proper order function
   * If the user has no plan: the order API should be called
   * If the user already has a plan the subscription upgrade API should be called
   */
  const getOrderFn = () => {
    if (isPaymentPlan(plan) && !subscription) {
      return () => handleOrder(plan);
    } else if (isPaymentPlan(plan) && subscription && !isSamePlan) {
      return () => upgradeSubscription(plan, subscription);
    }

    return null;
  };

  /**
   * Handles the update Account if the form data has been modified
   */
  const updateAccount = (account) => {
    const orderFn = getOrderFn();

    const wasAccountChanged = !isEqual(account, defaultFormValues);
    if (!wasAccountChanged && orderFn) {
      orderFn();
    } else if (wasAccountChanged) {
      const queryParams = {
        ...account,
        action: 'update',
        token: token,
      };

      setUpdateApi(
        mergeAPIParams({
          requestProps: {
            ...accountCall,
            watch: queryParams,
            params: {
              queryParams: queryParams,
            },
            config: {
              onResolve: orderFn,
            },
          },
        })
      );
    } else {
      enqueueSnackbar(t('account_did_not_change'), { variant: 'info' });
    }
  };

  /*
   * Handles the order request
   */
  const handleOrder = (plan) => {
    const queryParams = {
      token: token,
      plan: plan,
      success_url: 'http://localhost:3000/history?message=payment_succes',
      cancel_url: 'http://localhost:3000/history?message=payment_cancel',
    };
    setOrderPlanApi(
      mergeAPIParams({
        requestProps: {
          ...orderPlanCall,
          params: {
            queryParams: queryParams,
          },
          watch: queryParams,
        },
      })
    );
  };

  /**
   * Fetches the subscriptions
   */

  const { data: subscriptionsData, reload: reloadSubscriptions } = useAPI(
    mergeAPIParams({
      requestProps: {
        ...subscriptionsCall,
        params: {
          queryParams: {
            token: token,
          },
        },
      },
    })
  );

  useEffect(() => {
    const { successful } = getAPICallStatus(subscriptionsData);

    if (successful && subscriptionsData?.subscriptions) {
      setSubscription(subscriptionsData.subscriptions[0]);
    } else {
      setSubscription(null);
    }
  }, [subscriptionsData]);

  /**
   * Upgrades subscription
   */

  const [upgradeApi, setUpgradeApi] = useState({});
  const { data: upgradeData } = useAPI(upgradeApi);

  useEffect(() => {
    const { successful, message } = getAPICallStatus(upgradeData);

    if (successful) {
      setSubscription(upgradeData);
      enqueueSnackbar(t('plan_upgraded'), { variant: 'success' });
    } else if (message) {
      enqueueSnackbar(message, { variant: 'error' });
    }
  }, [upgradeData]);

  const upgradeSubscription = (plan, subscription) => {
    const { id } = subscription;
    const queryParams = {
      action: 'update',
      subscription: id,
      plan: plan,
      token: token,
    };

    setUpgradeApi(
      mergeAPIParams({
        requestProps: {
          ...subscriptionsCall,
          params: {
            queryParams: queryParams,
          },
          watch: queryParams,
        },
      })
    );
  };
  /**
   * Disables/enables the submit button of the account form
   */
  const disableAccountSubmitButton = (state) => {
    if (state !== accountButtonDisabled) {
      setAccountButtonDisabled(state);
    }
  };

  console.log(isSamePlan);
  return (
    <Box display='flex'>
      <NavBar {...navBar} position='fixed'></NavBar>
      <Sidebar {...sidebar}></Sidebar>
      <PageContent>
        <Grid container spacing={6}>
          <Grid item xs={12} lg={6}>
            <ContentCard
              title={t('accountSettings')}
              actionComponent={
                <Button
                  disabled={accountButtonDisabled}
                  variant='contained'
                  color='primary'
                  onClick={submitAccountForm}
                  autoFocus={isPaymentPlan(plan)}
                  focusRipple
                >
                  {t(isPaymentPlan(plan) && !isSamePlan ? 'order' : 'update')}
                </Button>
              }
            >
              <AccountForm
                ref={accountFormRef}
                {...accountForm}
                defaultValues={defaultFormValues}
                onSubmit={checkVat}
                error={formError}
                disableButton={disableAccountSubmitButton}
              ></AccountForm>
              {plan === 'free' && (
                <Box display='flex' justifyContent='flex-end' mt={3}>
                  <Button
                    variant='contained'
                    component={Link}
                    to='/history'
                    color='primary'
                  >
                    {t('skip')}
                  </Button>
                </Box>
              )}
            </ContentCard>
          </Grid>
          <Grid item xs={12} lg={6}>
            <Grid container spacing={6}>
              <Grid item xs={12}>
                <ContentCard
                  title={t('orders')}
                  actionComponent={
                    <Button
                      variant='contained'
                      color='primary'
                      component={Link}
                      to='/plans'
                    >
                      {t('upgrade')}
                    </Button>
                  }
                >
                  <CurrentOrder
                    {...currentOrder}
                    reloadSubscriptions={reloadSubscriptions}
                    subscription={subscription}
                    creditResetDate={accountData?.credit_reset_date}
                    credits={accountData?.credits}
                  />
                </ContentCard>
              </Grid>
              {
                <Grid item xs={12}>
                  <CreditsCard />
                </Grid>
              }
            </Grid>
          </Grid>
        </Grid>
      </PageContent>
    </Box>
  );
};

Account.propTypes = propTypes;
Account.defaultProps = defaultProps;

export default Account;
export { propTypes as AccountPropTypes, defaultProps as AccountDefaultProps };
