import { Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import shortid from 'shortid';
import i18n from '../../../i18n';
import Feature, { FeaturePropTypes } from '../Feature/Feature';
import { en_en } from './FeatureList.lang.en-en';
import { nl_be } from './FeatureList.lang.nl-be';
import { nl_nl } from './FeatureList.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'FeatureList', en_en);
i18n.addResourceBundle('nl-NL', 'FeatureList', nl_nl);
i18n.addResourceBundle('nl-BE', 'FeatureList', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  features: PropTypes.arrayOf(PropTypes.shape(FeaturePropTypes)),
};

/**
 * Defines the default props
 */
const defaultProps = {
  features: [
    {
      icon: 'settings',
      title: 'Feature 1',
      description:
        'Nulla eleifend pulvinar purus, molestie suismod odio imperdiet',
    },
    {
      icon: 'upload',
      title: 'Feature 2',
      description:
        'Nulla eleifend pulvinar purus, molestie suismod odio imperdiet',
    },
    {
      icon: 'printer',
      title: 'Feature 3',
      description:
        'Nulla eleifend pulvinar purus, molestie suismod odio imperdiet',
    },
  ],
};

/**
 * Displays the component
 */
const FeatureList = (props) => {
  const { features } = props;
  return (
    <Grid container spacing={4}>
      {features.map((feature) => {
        return <Feature key={shortid.generate()} {...feature}></Feature>;
      })}
    </Grid>
  );
};

FeatureList.propTypes = propTypes;
FeatureList.defaultProps = defaultProps;

export default FeatureList;
export {
  propTypes as FeatureListPropTypes,
  defaultProps as FeatureListDefaultProps,
};
