import PropTypes from 'prop-types';
import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  useLocation,
} from 'react-router-dom';
import Account, {
  AccountDefaultProps,
  AccountPropTypes,
} from '../saas/Account';
import Dashboard from '../saas/Dashboard';
import History, {
  HistoryDefaultProps,
  HistoryPropTypes,
} from '../saas/History';
import Login, { LoginDefaultProps, LoginPropTypes } from '../saas/Login';
import Register, {
  RegisterDefaultProps,
  RegisterPropTypes,
} from '../saas/Register';
import CompanyPopup from '../search/CompanyPopup';
import Search from '../search/Search';
import Users, { UsersDefaultProps, UsersPropTypes } from '../Users/Users';

/**
 * Defines the prop types
 */
const propTypes = {
  login: PropTypes.shape(LoginPropTypes),
  register: PropTypes.shape(RegisterPropTypes),
  account: PropTypes.shape(AccountPropTypes),
  history: PropTypes.shape(HistoryPropTypes),
  users: PropTypes.shape(UsersPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  login: LoginDefaultProps,
  register: RegisterDefaultProps,
  account: AccountDefaultProps,
  history: HistoryDefaultProps,
  users: UsersDefaultProps,
};

/**
 * Displays a company if an `fd` query param is present
 *
 * - Ex.: http://localhost:3000/?fd=NL12345678 or finsterdata.com?fd=NL12345678
 * - Due to `useLocation` this function has to be outside of <Routes>
 */
const CompanyByFD = () => {
  /**
   * Loads the query string
   */
  const queryString = new URLSearchParams(useLocation().search);

  return queryString.has('fd') ? (
    <CompanyPopup fd={queryString.get('fd')} />
  ) : (
    <Dashboard />
  );
};

/**
 * Displays the component
 */
const Routes = (props) => {
  const { login, account, history, register, users } = props;

  return (
    <Router>
      <Switch>
        <Route exact path='/search'>
          <Search />
        </Route>
        <Route exact path='/login'>
          <Login {...login} />
        </Route>
        <Route exact path='/register'>
          <Register {...register} />
        </Route>
        <Route exact path='/account'>
          <Account {...account} />
        </Route>
        <Route exact path={'/history'}>
          <History {...history} />
        </Route>
        <Route exact path={'/users'}>
          <Users {...users} />
        </Route>
        <Route path='/:section'>
          <Dashboard />
        </Route>
        <Route path='/'>
          <CompanyByFD />
        </Route>
      </Switch>
    </Router>
  );
};

Routes.propTypes = propTypes;
Routes.defaultProps = defaultProps;

export default Routes;
export { propTypes as RoutesPropTypes, defaultProps as RoutesDefaultProps };
