import {
  AppBar,
  Box,
  Button,
  Drawer,
  Hidden,
  IconButton,
  Link,
  makeStyles,
  Toolbar,
  useMediaQuery,
  useTheme,
} from '@material-ui/core';
import { Close as CloseIcon, Menu as MenuIcon } from '@material-ui/icons';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink, useHistory } from 'react-router-dom';
import { useAuth } from '../../hooks';
import i18n from '../../i18n';
import LanguagePicker, {
  LanguagePickerDefaultProps,
  LanguagePickerPropTypes,
} from '../LanguagePicker';
import Logo, { LogoDefaultProps, LogoPropTypes } from '../saas/Logo';
import ScrollLink from '../ScrollLink';
import { en_en } from './NavBar.lang.en-en';
import { nl_be } from './NavBar.lang.nl-be';
import { nl_nl } from './NavBar.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'NavBar', en_en);
i18n.addResourceBundle('nl-NL', 'NavBar', nl_nl);
i18n.addResourceBundle('nl-BE', 'NavBar', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  logo: PropTypes.shape(LogoPropTypes),
  languagePicker: PropTypes.shape(LanguagePickerPropTypes),
  variant: PropTypes.oneOf(['primary', 'transparent']),
  position: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  logo: LogoDefaultProps,
  languagePicker: LanguagePickerDefaultProps,
  variant: 'primary',
  position: 'static',
};

/**
 * Styles the component
 */

const useStyles = makeStyles((theme) => ({
  title: {
    flexGrow: 1,
    fontSize: '1.875rem',
    fontWeight: 900,
    lineHeight: '2.4375rem',
    textTransform: 'uppercase',
    color: theme.palette.common.white,
    marginLeft: theme.spacing(8),
  },
  appBar: (props) => ({
    backgroundColor:
      props.variant === 'primary' ? theme.palette.common.white : 'transparent',
    boxShadow: props.variant === 'transparent' ? 'none' : null,
    zIndex: theme.zIndex.drawer + 1,
  }),
  link: (props) => ({
    lineHeight: '1.3125rem',
    fontWeight: 900,
    fontSize: '1rem',
    textTransform: 'uppercase',
    marginRight: theme.spacing(8),
    cursor: 'pointer',
    '&:hover': {
      textDecoration: 'underline',
    },
  }),
  activeLink: {
    color: theme.palette.primary.main,
  },
  authLink: {
    [theme.breakpoints.up('xl')]: { marginLeft: theme.spacing(22.5) },
  },
  trialButton: {
    marginRight: theme.spacing(7),
  },
  toolbar: (props) => ({
    color:
      props.variant === 'primary'
        ? theme.palette.common.black
        : theme.palette.common.white,
    paddingRight: 0,
    // [theme.breakpoints.up('md')]: {
    //   height: 100,
    //   minHeight: 100
    // }
  }),
  drawerPaper: {
    width: 200,
  },
  closeMenuButton: {
    marginRight: 'auto',
    marginLeft: 0,
    marginBottom: theme.spacing(2),
  },
  drawerLinks: {
    paddingLeft: 14,
    display: 'flex',
    flexDirection: 'column',
    '& .MuiLink-root': {
      marginBottom: theme.spacing(2),
    },
  },
}));

/**
 * Displays the component
 */
const NavBar = (props) => {
  const { logo, languagePicker, position, variant } = props;
  const { t } = useTranslation('NavBar');
  const {
    appBar,
    activeLink,
    link,
    trialButton,
    authLink,
    toolbar,
    menuButton,
    drawerPaper,
    closeMenuButton,
    drawerLinks,
  } = useStyles(props);
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.up('sm'));
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const history = useHistory();
  const { isAuthenticated, logout } = useAuth();
  const [mobileOpen, setMobileOpen] = useState(false);

  /**
   * Redirects the user to the registration page
   */
  const handleButtonClick = () => {
    history.push('auth/register?plan=free');
  };

  /**
   * Handles the logout
   */
  const handleLogout = () => {
    logout();
  };

  /**
   * Toogles the mobile menu
   */
  const handleDrawerToggle = () => {
    if (isMobile) {
      setMobileOpen(!mobileOpen);
    }
  };

  /**
   * Generates the navigation links
   */
  const getLinks = () => {
    if (!isAuthenticated) {
      return (
        <>
          <ScrollLink
            className={link}
            activeClass={activeLink}
            to='features'
            hashSpy
            color='inherit'
            onClick={handleDrawerToggle}
          >
            {t('features', 'Features')}
          </ScrollLink>
          <ScrollLink
            className={link}
            activeClass={activeLink}
            to='pricing'
            onClick={handleDrawerToggle}
          >
            {t('pricing', 'Pricing')}
          </ScrollLink>

          <ScrollLink
            className={link}
            activeClass={activeLink}
            to='contact'
            onClick={handleDrawerToggle}
          >
            {t('contact', 'Contact')}
          </ScrollLink>

          <Link
            className={clsx(link, authLink)}
            component={RouterLink}
            to={'/login'}
            color='inherit'
            onClick={handleDrawerToggle}
          >
            {t('login', 'Login')}
          </Link>

          <Button
            className={trialButton}
            variant='contained'
            color='secondary'
            onClick={handleButtonClick}
          >
            {t('free_trial', 'Free trial')}
          </Button>
        </>
      );
    } else {
      return (
        <>
          <Link
            className={link}
            component={RouterLink}
            to={'/history'}
            color='inherit'
            onClick={handleDrawerToggle}
          >
            {t('history', 'History')}
          </Link>
          <Link
            className={link}
            component={RouterLink}
            to={'/account'}
            color='inherit'
            onClick={handleDrawerToggle}
          >
            {t('account', 'Account')}
          </Link>

          <Link
            className={link}
            component={RouterLink}
            to={'/'}
            onClick={handleLogout}
            color='inherit'
          >
            {t('logout', 'Logout')}
          </Link>
        </>
      );
    }
  };

  return (
    <>
      <AppBar className={appBar} position={position}>
        <Toolbar className={toolbar} disableGutters={matches}>
          <Hidden mdUp>
            <IconButton
              color='inherit'
              aria-label='Open drawer'
              edge={matches ? false : 'start'}
              onClick={handleDrawerToggle}
              className={menuButton}
            >
              <MenuIcon />
            </IconButton>
          </Hidden>
          <Hidden smDown>
            <Logo {...logo} variant={variant}></Logo>
            {getLinks()}
          </Hidden>
          <Hidden mdUp>
            <Link
              className={link}
              component={RouterLink}
              to={'/'}
              color='inherit'
            >
              Finsterdata
            </Link>
          </Hidden>
          <Box ml='auto'>
            <LanguagePicker
              {...languagePicker}
              variant={variant}
            ></LanguagePicker>
          </Box>
        </Toolbar>
      </AppBar>
      <nav>
        <Hidden mdUp implementation='css'>
          <Drawer
            variant='temporary'
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            <IconButton
              onClick={handleDrawerToggle}
              className={closeMenuButton}
            >
              <CloseIcon />
            </IconButton>
            <Box className={drawerLinks}>{getLinks()}</Box>
          </Drawer>
        </Hidden>
      </nav>
    </>
  );
};

NavBar.propTypes = propTypes;
NavBar.defaultProps = defaultProps;

export default NavBar;
export { propTypes as NavBarPropTypes, defaultProps as NavBarDefaultProps };
