import { Box, Grid, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import banner1 from '../../..//assets/feature-banner-1.svg';
import banner2 from '../../..//assets/feature-banner-2.svg';
import background1 from '../../..//assets/features-bg.png';
import background2 from '../../../assets/features-bg-2.png';
import i18n from '../../../i18n';
import DashboardDescription, {
  DashboardDescriptionDefaultProps,
  DashboardDescriptionPropTypes,
} from '../DashboardDescription';
import FeatureList, {
  FeatureListDefaultProps,
  FeatureListPropTypes,
} from '../FeatureList';
import Image, { ImageDefaultProps, ImagePropTypes } from '../Image';
import { en_en } from './Features.lang.en-en';
import { nl_be } from './Features.lang.nl-be';
import { nl_nl } from './Features.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Features', en_en);
i18n.addResourceBundle('nl-NL', 'Features', nl_nl);
i18n.addResourceBundle('nl-BE', 'Features', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  id: PropTypes.string,
  dashboardDescription: PropTypes.shape(DashboardDescriptionPropTypes),
  featureList: PropTypes.shape(FeatureListPropTypes),
  image: PropTypes.shape(ImagePropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  id: 'features',
  dashboardDescription: DashboardDescriptionDefaultProps,
  featureList: FeatureListDefaultProps,
  image: ImageDefaultProps,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  contentContainer: {
    paddingTop: theme.spacing(20),
  },
  subContentContainer: {
    paddingBottom: theme.spacing(20),
  },
  background1: {
    background: `url(${background1}) left top no-repeat, url(${background2}) left bottom no-repeat;`,
    backgroundSize: '100%, auto',
  },
  section: {
    marginBottom: theme.spacing(20),
    [theme.breakpoints.down('sm')]: {
      marginBottom: theme.spacing(5),
    },
  },
  statistics: {
    marginTop: -70,
    [theme.breakpoints.down('sm')]: {
      marginTop: 0,
    },
  },
}));

/**
 * Displays the component
 */
const Features = (props) => {
  const { id, dashboardDescription, featureList, image } = props;
  const {
    contentContainer,
    subContentContainer,
    section,
    statistics,
    background1,
  } = useStyles();
  const { t } = useTranslation('Features');

  return (
    <Box
      className={clsx(contentContainer, background1, 'homeContentContainer')}
    >
      <Box id={id} className={subContentContainer}>
        <Grid
          className={clsx(section)}
          container
          spacing={8}
          alignItems='flex-end'
        >
          <Grid item xs={12} lg={3}>
            <DashboardDescription
              {...dashboardDescription}
              title={t('title_1')}
              subtitle={t('subtitle_1')}
              description={t('description_1')}
            ></DashboardDescription>
          </Grid>
          <Grid item xs={12} lg={9}>
            <FeatureList {...featureList}></FeatureList>
          </Grid>
        </Grid>
        <Grid container className={section} spacing={8}>
          <Grid item xs={12} sm={6}>
            <Image
              {...image}
              className={statistics}
              src={banner1}
              objectFit='contain'
            ></Image>
          </Grid>
          <Grid item xs={12} sm={6}>
            <DashboardDescription
              {...dashboardDescription}
              title={t('title_2')}
              subtitle={t('subtitle_2')}
              description={t('description_2')}
            ></DashboardDescription>
          </Grid>
        </Grid>
        <Grid container spacing={8}>
          <Grid item xs={12} sm={7}>
            <DashboardDescription
              {...dashboardDescription}
              title={t('title_3')}
              subtitle={t('subtitle_3')}
              description={t('description_3')}
            ></DashboardDescription>
          </Grid>
          <Grid item xs={12} sm={5}>
            <Image {...image} src={banner2} objectFit='contain'></Image>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

Features.propTypes = propTypes;
Features.defaultProps = defaultProps;

export default Features;
export { propTypes as FeaturesPropTypes, defaultProps as FeaturesDefaultProps };
