import { Box, Grid, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import shortid from 'shortid';
import i18n from '../../../i18n';
import FooterLinkList, {
  FooterLinkListDefaultProps,
  FooterLinkListPropTypes,
} from '../FooterLinkList';
import { en_en } from './FooterLinks.lang.en-en';
import { nl_be } from './FooterLinks.lang.nl-be';
import { nl_nl } from './FooterLinks.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'FooterLinks', en_en);
i18n.addResourceBundle('nl-NL', 'FooterLinks', nl_nl);
i18n.addResourceBundle('nl-BE', 'FooterLinks', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  footerLinkList: PropTypes.shape(FooterLinkListPropTypes),
  linkGroups: PropTypes.arrayOf(PropTypes.shape(FooterLinkListPropTypes)),
};

/**
 * Defines the default props
 */
const defaultProps = {
  footerLinkList: FooterLinkListDefaultProps,
  linkGroups: [
    {
      title: i18n.t('FooterLinks:links'),
      links: [
        { text: i18n.t('FooterLinks:home'), to: '/' },
        { text: i18n.t('FooterLinks:features'), to: '/#features' },
        { text: i18n.t('FooterLinks:pricing'), to: '/#pricing' },
      ],
    },
    {
      title: null,
      links: [
        { text: i18n.t('FooterLinks:contact'), to: '/#contact' },
        {
          text: i18n.t('FooterLinks:login'),
          to: '/login',
          isReactLink: true,
        },
        {
          text: i18n.t('FooterLinks:register'),
          to: '/register',
          isReactLink: true,
        },
      ],
    },
    {
      title: i18n.t('FooterLinks:legal'),
      links: [
        { text: i18n.t('FooterLinks:terms'), to: '/' },
        { text: i18n.t('FooterLinks:privacy'), to: '/' },
        { text: i18n.t('FooterLinks:consent'), to: '/' },
      ],
    },
  ],
};

/**
 * Styles the component container
 */
const useStyles = makeStyles({
  linksContainer: {
    display: 'flex',
    flexGrow: 1,
    height: '100%',
  },
});

/**
 * Displays the component
 */
const FooterLinks = (props) => {
  const { footerLinkList, linkGroups } = props;
  const { linksContainer } = useStyles();
  return (
    <Box className={linksContainer}>
      {linkGroups.map((linkGroup) => {
        return (
          <Grid item xs={4} key={shortid.generate()}>
            <FooterLinkList
              {...footerLinkList}
              title={linkGroup.title}
              links={linkGroup.links}
            ></FooterLinkList>
          </Grid>
        );
      })}
    </Box>
  );
};

FooterLinks.propTypes = propTypes;
FooterLinks.defaultProps = defaultProps;

export default FooterLinks;
export {
  propTypes as FooterLinksPropTypes,
  defaultProps as FooterLinksDefaultProps,
};
