import { Box, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { scroller } from 'react-scroll';
import { UpgradesProvider } from '../../../hooks/useUpgrades';
import i18n from '../../../i18n';
import NavBar, { NavBarDefaultProps, NavBarPropTypes } from '../../NavBar';
import AppCard, { AppCardDefaultProps, AppCardPropTypes } from '../AppCard';
import Contact, { ContactDefaultProps, ContactPropTypes } from '../Contact';
import Features, { FeaturesDefaultProps, FeaturesPropTypes } from '../Features';
import Footer, { FooterDefaultProps, FooterPropTypes } from '../Footer';
import Pricing, { PricingDefaultProps, PricingPropTypes } from '../Pricing';
import { en_en } from './Dashboard.lang.en-en';
import { nl_be } from './Dashboard.lang.nl-be';
import { nl_nl } from './Dashboard.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Dashboard', en_en);
i18n.addResourceBundle('nl-NL', 'Dashboard', nl_nl);
i18n.addResourceBundle('nl-BE', 'Dashboard', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  mainPath: PropTypes.string,
  navBar: PropTypes.shape(NavBarPropTypes),
  appCard: PropTypes.shape(AppCardPropTypes),
  features: PropTypes.shape(FeaturesPropTypes),
  pricing: PropTypes.shape(PricingPropTypes),
  contact: PropTypes.shape(ContactPropTypes),
  footer: PropTypes.shape(FooterPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  mainPath: '/',
  navBar: NavBarDefaultProps,
  appCard: AppCardDefaultProps,
  features: FeaturesDefaultProps,
  pricing: PricingDefaultProps,
  contact: ContactDefaultProps,
  footer: FooterDefaultProps,
};
/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  dashboard: {
    '& .homeContentContainer': {
      paddingLeft: theme.spacing(20),
      paddingRight: theme.spacing(20),
      overflow: 'hidden',

      [theme.breakpoints.down('lg')]: {
        paddingLeft: theme.spacing(10),
        paddingRight: theme.spacing(10),
      },
      [theme.breakpoints.down('md')]: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
      },
    },
  },
}));
/**
 * Displays the component
 */
const Dashboard = (props) => {
  const { navBar, appCard, features, pricing, contact, footer } = props;
  const { dashboard } = useStyles();
  const { section } = useParams();

  /**
   * Scrolls to anchor element
   * */
  const scrollToAnchor = (section) => {
    setTimeout(() => {
      scroller.scrollTo(section, {
        duration: 500,
        smooth: true,
        delay: 500,
      });
    });
  };

  /**
   * Scroll to anchor element on mount
   * */
  useEffect(() => {
    if (section) {
      scrollToAnchor(section);
    }
  });

  return (
    <UpgradesProvider>
      <Box className={dashboard}>
        <NavBar {...navBar} position='fixed'></NavBar>
        <AppCard {...appCard} />
        <Features {...features} />
        <Pricing {...pricing} />
        <Contact {...contact}></Contact>
        <Footer {...footer}></Footer>
      </Box>
    </UpgradesProvider>
  );
};

Dashboard.propTypes = propTypes;
Dashboard.defaultProps = defaultProps;

export default Dashboard;
export {
  propTypes as DashboardPropTypes,
  defaultProps as DashboardDefaultProps,
};
