import {
  Box,
  Link,
  ListItem,
  ListItemIcon,
  ListItemText,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import i18n from '../../i18n';
import Image from '../saas/Image';
import { en_en } from './SidebarItem.lang.en-en';
import { nl_be } from './SidebarItem.lang.nl-be';
import { nl_nl } from './SidebarItem.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'SidebarItem', en_en);
i18n.addResourceBundle('nl-NL', 'SidebarItem', nl_nl);
i18n.addResourceBundle('nl-BE', 'SidebarItem', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  icon: PropTypes.any,
  text: PropTypes.string,
  to: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  icon: null,
  text: '',
  to: '/',
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  listItem: {
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
  },
  iconParent: {
    width: theme.mixins.toolbar.height,
    display: 'flex',
    justifyContent: 'center',
  },
  iconContainer: {
    width: 45,
  },
}));
/**
 * Displays the component
 */
const SidebarItem = (props) => {
  const { icon, text, to } = props;
  const { iconContainer, iconParent, listItem } = useStyles();
  // return <SvgIcon component={icon} />;
  return (
    <Link component={RouterLink} to={to} color='inherit'>
      <ListItem disableGutters button key={text} className={listItem}>
        <ListItemIcon>
          <Box className={iconParent}>
            <Box className={iconContainer}>
              <Image src={icon} />
            </Box>
          </Box>
        </ListItemIcon>
        <ListItemText primary={text} />
      </ListItem>
    </Link>
  );
};

SidebarItem.propTypes = propTypes;
SidebarItem.defaultProps = defaultProps;

export default SidebarItem;
export {
  propTypes as SidebarItemPropTypes,
  defaultProps as SidebarItemDefaultProps,
};
