import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Grid,
  makeStyles,
  Typography,
} from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../../../hooks';
import { useUpgrades } from '../../../hooks/useUpgrades';
import i18n from '../../../i18n';
import Currency from '../Currency';
import PackageName from '../PackageName';
import PlanItems from '../PlanItems';
import { en_en } from './Plan.lang.en-en';
import { nl_be } from './Plan.lang.nl-be';
import { nl_nl } from './Plan.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Plan', en_en);
i18n.addResourceBundle('nl-NL', 'Plan', nl_nl);
i18n.addResourceBundle('nl-BE', 'Plan', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  plan: PropTypes.string,
  amount: PropTypes.string,
  currency: PropTypes.string,
  interval: PropTypes.string,
  nickname: PropTypes.string,
  product: PropTypes.string,
  package: PropTypes.string,
  credits: PropTypes.string,
  highlight: PropTypes.bool,
};

/**
 * Defines the default props
 */
const defaultProps = {
  plan: 'plan_bronze_year_eur_v2018_500',
  amount: '-',
  currency: 'eur',
  interval: 'year',
  nickname: 'bronze_yearly',
  product: 'prod_company_v2018',
  package: 'bronze',
  credits: '500',
  highlight: false,
};

/**
 * Styles the component
 */

const useStyles = makeStyles((theme) => ({
  buttons: {
    flexDirection: 'column',
  },

  priceContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'flex-end',
  },
  card: {
    borderRadius: 20,
    boxShadow: '0px 1px 3px #00000029',
    height: 600,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    padding: `${theme.spacing(4)}px 0`,
    border: `4px solid ${theme.palette.common.grey}`,
  },
  highlighted: {
    border: `6px solid ${theme.palette.primary.main}`,
    height: 660,
  },
  title: (props) => ({
    fontWeight: 900,
    marginBottom: props.highlight ? theme.spacing(6) : theme.spacing(5),
  }),
  monthLabel: (props) => ({
    letterSpacing: 0,
    fontWeight: 700,
    fontSize: props.highlight ? '2.3125rem' : '1.8125rem',
    lineHeight: 2.2,
    [theme.breakpoints.down('sm')]: {
      lineHeight: props.hightlight ? 1.6 : 1.7,
    },
  }),
  priceLabel: {
    display: 'flex',
    alignItems: 'flex-end',
  },

  trialBtn: {
    marginTop: theme.spacing(1),
  },
}));

/**
 * Returns the path of the redirect
 */
const getSubscriptionPath = (isAuthenticated, activePlan) => {
  if (isAuthenticated) {
    return '/account';
  }
  return '/register';
};

/**
 * Formats the amount. If it's a round number we don't need the decimals
 */
const formatAmount = (amount) => {
  if (amount.toString().includes('.00')) {
    return Math.floor(amount);
  } else {
    return amount;
  }
};

/**
 * Displays the component
 */
const Plan = (props) => {
  const {
    buttons,
    priceContainer,
    highlighted,
    title,
    monthLabel,
    card,
    trialBtn,
    priceLabel,
  } = useStyles(props);
  const { activeSubscription } = useUpgrades();
  const {
    amount,
    credits,
    currency,
    package: planPackage,
    highlight,
    plan,
  } = props;
  const { t } = useTranslation('Plan');
  const history = useHistory();
  const { isAuthenticated } = useAuth();
  const getHighlightedClass = (highlight) => {
    return highlight ? highlighted : '';
  };

  const handleOptionClick = (option) => {
    const path = getSubscriptionPath(isAuthenticated, activeSubscription);
    if (option === 'trial') {
      history.push(`${path}?plan=trial`);
    } else {
      history.push(`${path}?plan=${plan}`);
    }
  };

  return (
    <Grid item xs={12} sm={6} md={4}>
      <Card className={clsx(card, getHighlightedClass(highlight))}>
        <CardHeader
          title={<PackageName plan={planPackage} />}
          titleTypographyProps={{
            align: 'center',
            variant: 'h4',
            className: title,
          }}
          subheader={
            <Box className={priceContainer}>
              <Typography
                className={priceLabel}
                variant={highlight ? 'h1' : 'h2'}
                color='textPrimary'
              >
                <Currency currency={currency} /> {formatAmount(amount)}
              </Typography>
              <Typography className={monthLabel} variant='h4' color='primary'>
                &nbsp;/&nbsp;{t('month', 'mo')}
              </Typography>
            </Box>
          }
        />
        <CardContent>
          {/* <ul className={list}>{renderPlanItems()}</ul> */}
          <PlanItems plan={planPackage} credits={credits} />
        </CardContent>
        <CardActions className={buttons}>
          <Box width={0.75}>
            <Button
              fullWidth
              color='primary'
              variant={highlight ? 'contained' : 'outlined'}
              onClick={() => handleOptionClick('buy')}
            >
              {t('buy_now', 'Buy now')}
            </Button>
            <br />
            {!isAuthenticated && (
              <Button
                className={trialBtn}
                fullWidth
                color='primary'
                onClick={() => {
                  handleOptionClick('trial');
                }}
              >
                {t('free_trial', 'Free trial')}
              </Button>
            )}
          </Box>
        </CardActions>
      </Card>
    </Grid>
  );
};

Plan.propTypes = propTypes;
Plan.defaultProps = defaultProps;

export default Plan;
export { propTypes as PlanPropTypes, defaultProps as PlanDefaultProps };
