import PropTypes from 'prop-types';
import React from 'react';
import i18n from '../../../i18n';
import { en_en } from './Anchor.lang.en-en';
import { nl_be } from './Anchor.lang.nl-be';
import { nl_nl } from './Anchor.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Anchor', en_en);
i18n.addResourceBundle('nl-NL', 'Anchor', nl_nl);
i18n.addResourceBundle('nl-BE', 'Anchor', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  id: PropTypes.string,
  top: PropTypes.number,
};

/**
 * Defines the default props
 */
const defaultProps = {
  id: '',
  top: 0,
};

/**
 * Displays the component
 */
const Anchor = (props) => {
  const { id, top } = props;
  return <div id={id} style={{ position: 'absolute', top }} />;
};

Anchor.propTypes = propTypes;
Anchor.defaultProps = defaultProps;

export default Anchor;
export { propTypes as AnchorPropTypes, defaultProps as AnchorDefaultProps };
