import { Box, Button, Collapse, IconButton } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Close } from '@material-ui/icons';
import Alert from '@material-ui/lab/Alert';
import parse from 'html-react-parser';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import i18n from '../../../i18n';
import { en_en } from './OverviewMessage.lang.en-en';
import { nl_be } from './OverviewMessage.lang.nl-be';
import { nl_nl } from './OverviewMessage.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'OverviewMessage', en_en);
i18n.addResourceBundle('nl-NL', 'OverviewMessage', nl_nl);
i18n.addResourceBundle('nl-BE', 'OverviewMessage', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  message: PropTypes.string,
  page: PropTypes.string,
  onCanceled: PropTypes.func,
  spacing: PropTypes.number,
};

/**
 * Defines the default props
 */
const defaultProps = {
  messages: '',
  page: '/',
  onCanceled: () => {},
  spacing: 2,
};

const paths = {
  order: '/account',
  price: '/account',
  subscriptions: '/',
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  iconButton: {
    marginLeft: theme.spacing(1),
  },
  alert: (props) => ({
    marginBottom: theme.spacing(props.spacing),
  }),
}));

/**
 * Returns the page to which the user will be redirected
 */
const getPage = (page) => {
  return paths[page];
};

/**
 * Displays the component
 */
const OverviewMessage = (props) => {
  const { message, page, onCanceled } = props;
  const { iconButton, alert } = useStyles(props);

  const { t } = useTranslation('OverviewMessage');

  const history = useHistory();

  const [open, setOpen] = useState(true);

  const closeMessage = () => {
    setOpen(false);
  };

  const navigateToPage = () => {
    history.push(getPage(page));
  };

  return (
    <Box width={1}>
      <Collapse in={open} onExited={() => onCanceled(message)}>
        <Alert
          severity='info'
          className={alert}
          action={
            <>
              <Button size='small' onClick={navigateToPage}>
                {t('more_info')}
              </Button>
              <IconButton
                className={iconButton}
                onClick={closeMessage}
                size='small'
                component='span'
              >
                <Close></Close>
              </IconButton>
            </>
          }
        >
          {parse(message)}
        </Alert>
      </Collapse>
    </Box>
  );
};

OverviewMessage.propTypes = propTypes;
OverviewMessage.defaultProps = defaultProps;

export default OverviewMessage;
export {
  propTypes as OverviewMessagePropTypes,
  defaultProps as OverviewMessageDefaultProps,
};
