import { Box, Grid, makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import contactBg from '../../../assets/contact-bg.png';
import i18n from '../../../i18n';
import ConctactCard, {
  ConctactCardDefaultProps,
  ConctactCardPropTypes,
} from '../ConctactCard';
import DashboardDescription, {
  DashboardDescriptionDefaultProps,
  DashboardDescriptionPropTypes,
} from '../DashboardDescription';
import { SocialMediaIconDefaultProps } from '../SocialMediaIcon';
import SocialMediaIconList, {
  SocialMediaIconListPropTypes,
} from '../SocialMediaIconList';
import { en_en } from './Contact.lang.en-en';
import { nl_be } from './Contact.lang.nl-be';
import { nl_nl } from './Contact.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Contact', en_en);
i18n.addResourceBundle('nl-NL', 'Contact', nl_nl);
i18n.addResourceBundle('nl-BE', 'Contact', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  id: PropTypes.string,
  dashboardDescription: PropTypes.shape(DashboardDescriptionPropTypes),
  contactCard: PropTypes.shape(ConctactCardPropTypes),
  socialMediaIconList: PropTypes.shape(SocialMediaIconListPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  id: 'contact',
  dashboardDescription: DashboardDescriptionDefaultProps,
  contactCard: ConctactCardDefaultProps,
  socialMediaIconList: SocialMediaIconDefaultProps,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  contentContainer: {
    paddingBottom: theme.spacing(10),
    background: `transparent url(${contactBg}) 0% 0% no-repeat padding-box; `,
    backgroundPosition: 'bottom left',
  },
  contactInfo: {
    fontWeight: 900,
    marginBottom: theme.spacing(2),
  },
  email: {
    marginTop: theme.spacing(3),
  },
  followUs: {
    [theme.breakpoints.down('xs')]: {
      marginBottom: theme.spacing(2),
    },
  },
  socialMediaContainer: {
    [theme.breakpoints.up('sm')]: {
      display: 'flex',
      alignItems: 'center',
    },
  },
}));

/**
 * Displays the component
 */
const Contact = (props) => {
  const { id, contactCard, dashboardDescription, socialMediaIconList } = props;
  const {
    contentContainer,
    contactInfo,
    email,
    followUs,
    socialMediaContainer,
  } = useStyles();
  const { t } = useTranslation('Contact');
  return (
    <Box id={id} className={clsx(contentContainer, 'homeContentContainer')}>
      <Grid container spacing={10}>
        <Grid item xs={12} lg={5}>
          <DashboardDescription
            {...dashboardDescription}
            title={t('title')}
            subtitle={t('subtitle')}
            description={t('description')}
          >
            <Typography
              className={clsx(contactInfo, email)}
              variant='h4'
              color='primary'
            >
              office@finsterdata.dk
            </Typography>
            <Typography className={contactInfo} variant='h4' color='primary'>
              (573)334-6995
            </Typography>
            <Box className={socialMediaContainer}>
              <Box mr={4}>
                <Typography className={followUs} variant='body1'>
                  {t('follow_us', 'Follow us on')}
                </Typography>
              </Box>
              <SocialMediaIconList {...socialMediaIconList} spacing={2} />
            </Box>
          </DashboardDescription>
        </Grid>
        <Grid item xs={12} lg={7}>
          <ConctactCard {...contactCard}></ConctactCard>
        </Grid>
      </Grid>
    </Box>
  );
};

Contact.propTypes = propTypes;
Contact.defaultProps = defaultProps;

export default Contact;
export { propTypes as ContactPropTypes, defaultProps as ContactDefaultProps };
