import { Box, makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import i18n from '../../../i18n';
import { en_en } from './DashboardDescription.lang.en-en';
import { nl_be } from './DashboardDescription.lang.nl-be';
import { nl_nl } from './DashboardDescription.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'DashboardDescription', en_en);
i18n.addResourceBundle('nl-NL', 'DashboardDescription', nl_nl);
i18n.addResourceBundle('nl-BE', 'DashboardDescription', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  description: PropTypes.string,
  inverted: PropTypes.bool,
};

/**
 * Defines the default props
 */
const defaultProps = {
  title: 'Title',
  subtitle: 'Subtitle',
  description: 'Description',
  inverted: false,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
  },
  text: (props) => ({
    color: props.inverted
      ? theme.palette.common.white
      : theme.palette.common.black,
  }),
  titleText: {
    marginBottom: theme.spacing(1.375),
  },
  subtitleText: {
    marginBottom: theme.spacing(6),
  },
  descriptionText: {
    lineHeight: '2.5625rem',
  },
}));

/**
 * Displays the component
 */
const DashboardDescription = (props) => {
  const { title, subtitle, description, children } = props;
  const {
    container,
    text,
    titleText,
    subtitleText,
    descriptionText,
  } = useStyles(props);
  return (
    <Box className={container}>
      <Typography className={clsx(text, titleText)} variant='h3'>
        {title}
      </Typography>
      <Typography className={clsx(text, subtitleText)} variant='h4'>
        {subtitle}
      </Typography>
      <Typography className={clsx(text, descriptionText)} variant='body1'>
        {description}
      </Typography>
      {children}
    </Box>
  );
};

DashboardDescription.propTypes = propTypes;
DashboardDescription.defaultProps = defaultProps;

export default DashboardDescription;
export {
  propTypes as DashboardDescriptionPropTypes,
  defaultProps as DashboardDescriptionDefaultProps,
};
