import { Box, makeStyles } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import laptop from '../../../assets/laptop-group-right.png';
import linesImg from '../../../assets/lines.png';
import Image, { ImageDefaultProps, ImagePropTypes } from '../Image';
import Logo from '../Logo';

/**
 * Defines the prop types
 */
const propTypes = {
  image: PropTypes.shape(ImagePropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  image: ImageDefaultProps,
};

const useStyles = makeStyles((theme) => ({
  container: {
    height: '100%',
    position: 'relative',
    overflow: 'hidden',
    background: `url(${linesImg}), linear-gradient(180deg, #F9BB51 0%, #C75300 100%)`,
  },
  img: {
    position: 'absolute',
    left: 0,
    top: '150px',
  },
  logoContainer: {
    display: 'flex',
    alignItems: 'center',
  },
}));

/**
 * Displays the component
 */

const AuthImage = (props) => {
  const { image } = props;
  const { img, container, logoContainer } = useStyles();

  // return <img className={img} src={linesImg} alt='' />;
  return (
    <Box className={container} width={1}>
      <Box className={logoContainer}>
        <Logo variant='transparent'></Logo>
      </Box>
      <Image
        {...image}
        className={img}
        src={laptop}
        maxHeight={`${document.body.scrollHeight - 200}px`}
        width='auto'
      ></Image>
    </Box>
  );
};

AuthImage.propTypes = propTypes;
AuthImage.defaultProps = defaultProps;

export default AuthImage;
export {
  propTypes as AuthImagePropTypes,
  defaultProps as AuthImageDefaultProps,
};
