import { Box, Link, makeStyles, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { ReactComponent as LogoSVG } from '../../../assets/logo.svg';

/**
 * Defines the prop types
 */
const propTypes = {
  variant: PropTypes.oneOf(['primary', 'transparent']),
  noPadding: PropTypes.bool,
};

/**
 * Defines the default props
 */
const defaultProps = {
  variant: 'primary',
  noPadding: false,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  logoContainer: {
    display: 'flex',
    flexGrow: 1,
    alignItems: 'center',
    ...theme.mixins.toolbar,
  },
  title: (props) => ({
    flexGrow: 1,
    fontSize: '1.875rem',
    fontWeight: 900,
    lineHeight: '2.4375rem',
    textTransform: 'uppercase',
    color:
      props.variant === 'primary'
        ? theme.palette.common.black
        : theme.palette.common.white,
    marginLeft: theme.spacing(5),
  }),
  logo: (props) => ({
    display: 'flex',
    alignItems: 'center',
    height: 100,
    width: props.noPadding ? 'auto' : 100,
    justifyContent: 'center',
    background:
      props.variant === 'primary'
        ? `transparent linear-gradient(180deg, #F9BB51 0%, #C75300 100%) 0% 0% no-repeat padding-box`
        : 'transparent',
  }),
}));

/**
 * Displays the component
 */
const Logo = (props) => {
  const { noPadding } = props;
  const { logo, title, logoContainer } = useStyles(props);
  return (
    <Box className={logoContainer}>
      <Box className={logo} style={noPadding ? { padding: 0 } : {}}>
        <LogoSVG />
      </Box>
      <Typography className={title} variant='h6'>
        <Link component={RouterLink} to='/' color='inherit'>
          {'Finsterdata'}
        </Link>
      </Typography>
    </Box>
  );
};

Logo.propTypes = propTypes;
Logo.defaultProps = defaultProps;

export default Logo;
export { propTypes as LogoPropTypes, defaultProps as LogoDefaultProps };
