import { Box, Grid, makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import footerImg from '../../..//assets/footer.png';
import i18n from '../../../i18n';
import FooterLinks, {
  FooterLinksDefaultProps,
  FooterLinksPropTypes,
} from '../FooterLinks';
import Logo, { LogoDefaultProps, LogoPropTypes } from '../Logo';
import { en_en } from './Footer.lang.en-en';
import { nl_be } from './Footer.lang.nl-be';
import { nl_nl } from './Footer.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Footer', en_en);
i18n.addResourceBundle('nl-NL', 'Footer', nl_nl);
i18n.addResourceBundle('nl-BE', 'Footer', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  logo: PropTypes.shape(LogoPropTypes),
  footerLinks: PropTypes.shape(FooterLinksPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  logo: LogoDefaultProps,
  footerLinks: FooterLinksDefaultProps,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  footer: {
    background: `transparent url(${footerImg}) 0% 0% no-repeat padding-box`,
    backgroundPosition: 'left top',
    backgroundSize: 'cover',
    paddingTop: theme.spacing(6),
    paddingBottom: theme.spacing(6),
    color: theme.palette.text.white,
  },
  container: {
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column-reverse',
    },
  },
  logoContainer: {
    [theme.breakpoints.down('sm')]: {
      marginBottom: theme.spacing(2),
    },
  },
  copyright: {
    [theme.breakpoints.down('sm')]: {
      marginBottom: theme.spacing(4),
    },
  },
}));

/**
 * Displays the component
 */
const Footer = (props) => {
  const { footerLinks, logo } = props;
  const { footer, logoContainer, copyright, container } = useStyles();
  const { t } = useTranslation('Footer');
  return (
    <Box className={clsx(footer, 'homeContentContainer')}>
      <Grid className={container} container>
        <Grid item xs={12} lg={4}>
          <Box
            display='flex'
            flexDirection='column'
            justifyContent='space-between'
            height={1}
          >
            <Box className={logoContainer}>
              <Logo {...logo} variant='transparent' noPadding></Logo>
            </Box>
            <Typography className={copyright} variant='body1' color='inherit'>
              {t('copyright', ' Finster data. All rights reserved')}
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} lg={8}>
          <Box mb={8}>
            <FooterLinks {...footerLinks}></FooterLinks>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

Footer.propTypes = propTypes;
Footer.defaultProps = defaultProps;

export default Footer;
export { propTypes as FooterPropTypes, defaultProps as FooterDefaultProps };
