import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import i18n from '../../../i18n';
import { en_en } from './PackageName.lang.en-en';
import { nl_be } from './PackageName.lang.nl-be';
import { nl_nl } from './PackageName.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'PackageName', en_en);
i18n.addResourceBundle('nl-NL', 'PackageName', nl_nl);
i18n.addResourceBundle('nl-BE', 'PackageName', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  plan: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  plan: 'bronze',
};

/**
 * Displays the component
 */
const PackageName = (props) => {
  const { plan } = props;
  const { t } = useTranslation('PackageName');

  const getPackageName = (plan) => {
    if (!plan) {
      return '';
    }

    if (plan.includes('bronze')) {
      return t('bronze', 'Bronze');
    } else if (plan.includes('silver')) {
      return t('silver', 'Silver');
    } else if (plan.includes('gold')) {
      return t('gold', 'Gold');
    } else if (plan === 'free') {
      return t('free', 'Free trial');
    } else {
      return '';
    }
  };

  return <>{getPackageName(plan)}</>;
};

PackageName.propTypes = propTypes;
PackageName.defaultProps = defaultProps;

export default PackageName;
export {
  propTypes as PackageNamePropTypes,
  defaultProps as PackageNameDefaultProps,
};
