import { Box, Grid, makeStyles, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import i18n from '../../../i18n';
import FeatureIcon from '../FeatureIcon';
import { en_en } from './Feature.lang.en-en';
import { nl_be } from './Feature.lang.nl-be';
import { nl_nl } from './Feature.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Feature', en_en);
i18n.addResourceBundle('nl-NL', 'Feature', nl_nl);
i18n.addResourceBundle('nl-BE', 'Feature', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  icon: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  icon: 'icon',
  title: 'Feature title',
  description: 'Feature description',
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  feature: {
    maxHeight: 500,
    backgroundColor: theme.palette.common.white,
    padding: theme.spacing(3),
    boxShadow: '0px 1px 3px #00000033',
    borderRadius: 10,
  },
  titleText: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    fontWeight: 900,
  },
  descriptionText: {
    lineHeight: '1.9375rem',
  },
}));

/**
 * Displays the component
 */
const Feature = (props) => {
  const { icon, title, description } = props;
  const { feature, titleText, descriptionText } = useStyles();
  return (
    <Grid item xs={12} lg={4}>
      <Box className={feature}>
        <FeatureIcon icon={icon}></FeatureIcon>
        <Typography className={titleText} variant='h4'>
          {title}
        </Typography>
        <Typography className={descriptionText} variant='body1'>
          {description}
        </Typography>
      </Box>
    </Grid>
  );
};

Feature.propTypes = propTypes;
Feature.defaultProps = defaultProps;

export default Feature;
export { propTypes as FeaturePropTypes, defaultProps as FeatureDefaultProps };
