import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import i18n from '../../../i18n';
import { en_en } from './Interval.lang.en-en';
import { nl_be } from './Interval.lang.nl-be';
import { nl_nl } from './Interval.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Interval', en_en);
i18n.addResourceBundle('nl-NL', 'Interval', nl_nl);
i18n.addResourceBundle('nl-BE', 'Interval', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  interval: PropTypes.oneOf(['year', 'month']),
};

/**
 * Defines the default props
 */
const defaultProps = {
  interval: 'month',
};

/**
 * Displays the component
 */
const Interval = (props) => {
  const { interval } = props;
  const { t } = useTranslation('Interval');

  const getInterval = (interval) => {
    switch (interval) {
      case 'month':
        return t('month');
      case 'year':
        return t('year');
      default:
        return '';
    }
  };
  return <>{getInterval(interval)}</>;
};

Interval.propTypes = propTypes;
Interval.defaultProps = defaultProps;

export default Interval;
export { propTypes as IntervalPropTypes, defaultProps as IntervalDefaultProps };
