import {
  Box,
  Grid,
  Hidden,
  makeStyles,
  Typography,
  Paper,
} from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useGoogleReCaptcha } from 'react-google-recaptcha-v3';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../../../hooks';
import useAPI, {
  getAPICallStatus,
  mergeAPIParams,
} from '../../../hooks/useAPI';
import useQuery from '../../../hooks/useQuery/useQuery';
import i18n from '../../../i18n';
import AuthFooter from '../AuthFooter';
import AuthImage, {
  AuthImageDefaultProps,
  AuthImagePropTypes,
} from '../AuthImage';
import AuthLogo from '../AuthLogo';
import RegisterForm, {
  RegisterFormDefaultProps,
  RegisterFormPropTypes,
} from '../RegisterForm';
import { en_en } from './Register.lang.en-en';
import { nl_be } from './Register.lang.nl-be';
import { nl_nl } from './Register.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Register', en_en);
i18n.addResourceBundle('nl-NL', 'Register', nl_nl);
i18n.addResourceBundle('nl-BE', 'Register', nl_be);
/**
 * Defines the prop types
 */
const propTypes = {
  registerForm: PropTypes.shape(RegisterFormPropTypes),
  authImage: PropTypes.shape(AuthImagePropTypes),
  defaultPlan: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  registerForm: RegisterFormDefaultProps,
  authImage: AuthImageDefaultProps,
  defaultPlan: 'free',
};

/**
 * Styles the component
 */

const useStyles = makeStyles((theme) => ({
  container: {
    height: '100vh',
  },

  formContainer: {
    ...theme.mixins.center,
    padding: '4rem',
    flexDirection: 'column',
    height: '100%',
  },
  formTitle: {
    marginTop: theme.spacing(7),
    marginBottom: theme.spacing(7),
  },
  registerForm: {
    width: '100%',
  },
}));

/**
 * Displays the component
 */

const Register = (props) => {
  const { registerForm, authImage, defaultPlan } = props;
  const history = useHistory();
  const { isAuthenticated } = useAuth();

  /**
   * If the user is already authenticated redirect to the homepage
   */
  if (isAuthenticated) {
    history.push('/');
  }

  const { container, formContainer, formTitle } = useStyles();
  const { t, i18n } = useTranslation('Register');
  const query = useQuery();
  const { register } = useAuth();
  const { executeRecaptcha } = useGoogleReCaptcha();
  const [formError, setFormError] = useState(null);
  const [params, setParams] = useState({});
  const { data } = useAPI(params);

  useEffect(() => {
    const { successful, message } = getAPICallStatus(data);

    if (successful) {
      register(data);
      setFormError(null);
      const plan = query.get('plan');
      history.push(`/account?plan=${plan || defaultPlan}`);
    } else {
      setFormError(message);
    }
  }, [data, register, defaultPlan, query, history]);

  const handleSubmit = async (inputs) => {
    const { email, name, password, terms } = inputs;
    try {
      const token = await executeRecaptcha('register');
      setParams(
        mergeAPIParams({
          requestProps: {
            path: {
              endpoint: 'register',
            },
            params: {
              queryParams: {
                email,
                password,
                name,
                agree: terms ? 'on' : 'off',
                recaptcha_response: token,
                fd_lang: i18n.language,
              },
            },
          },
        })
      );
    } catch (e) {
      console.error(e);
    }
  };

  return (
    <Grid container component='main' className={container}>
      <Grid item xs={false} sm={4} md={7}>
        <Hidden xsDown>
          <AuthImage {...authImage} />
        </Hidden>
      </Grid>
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={0} square>
        <Box className={formContainer}>
          <AuthLogo />
          <Typography className={formTitle} variant='h3' align='center'>
            {t('title', 'Register to Finsterdata')}
          </Typography>
          <RegisterForm
            {...registerForm}
            onSubmit={handleSubmit}
            error={formError}
          ></RegisterForm>
          <Box mt={5}>
            <AuthFooter></AuthFooter>
          </Box>
        </Box>
      </Grid>
    </Grid>
  );
};

Register.propTypes = propTypes;
Register.defaultProps = defaultProps;

export default Register;
export { propTypes as RegisterPropTypes, defaultProps as RegisterDefaultProps };
