import { Drawer, Hidden, List, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import shortid from 'shortid';
import FactorySVG from '../../assets/factory.svg';
import WebsiteSVG from '../../assets/website.svg';
import i18n from '../../i18n';
import SidebarItem, { SidebarItemPropTypes } from '../SidebarItem';
import { en_en } from './Sidebar.lang.en-en';
import { nl_be } from './Sidebar.lang.nl-be';
import { nl_nl } from './Sidebar.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Sidebar', en_en);
i18n.addResourceBundle('nl-NL', 'Sidebar', nl_nl);
i18n.addResourceBundle('nl-BE', 'Sidebar', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  menuItems: PropTypes.arrayOf(PropTypes.shape(SidebarItemPropTypes)),
};

/**
 * Defines the default props
 */
const defaultProps = {
  menuItems: [
    {
      icon: WebsiteSVG,
      text: i18n.t('Sidebar:link_1'),
      to: '/website',
    },
    {
      icon: FactorySVG,
      text: i18n.t('Sidebar:link_2'),
      to: '/factory',
    },
  ],
};

/**
 * Styles the component container
 */

const useStyles = makeStyles((theme) => ({
  drawer: {
    flexShrink: 0,
  },
  drawerWidth: {
    width: theme.mixins.toolbar.height,
    transition: 'width 0.3s ease-in',
    '& &:hover': {
      width: 200,
      '& .MuiListItemText-root': {
        visibility: 'visible',
      },
    },
    '& .MuiListItemText-root': {
      visibility: 'hidden',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
}));

/**
 * Displays the component
 */
const Sidebar = (props) => {
  const { menuItems } = props;
  const { drawer, drawerWidth, list, toolbar } = useStyles();

  return (
    <Hidden smDown>
      <Drawer
        className={clsx(drawer, drawerWidth)}
        variant='permanent'
        classes={{
          paper: drawerWidth,
        }}
      >
        <div className={toolbar} />
        <List className={list}>
          {menuItems.map((item) => (
            <SidebarItem
              key={shortid.generate()}
              to={item.to}
              icon={item.icon}
              text={item.text}
            />
          ))}
        </List>
      </Drawer>
    </Hidden>
  );
};

Sidebar.propTypes = propTypes;
Sidebar.defaultProps = defaultProps;

export default Sidebar;
export { propTypes as SidebarPropTypes, defaultProps as SidebarDefaultProps };
