import { IconButton, ThemeProvider } from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { SnackbarProvider } from 'notistack';
import React, { Suspense, useRef } from 'react';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';
import { AuthProvider, ConfirmationProvider } from '../../hooks';
import theme from '../../theme';

// Make sure to call `loadStripe` outside of a components render to avoid
// recreating the `Stripe` object on every render.
const stripePromise = loadStripe('pk_test_xvCE9TKDU6M5yDzkRDbCeAWU');

/**
 * Displays the component
 */
const Providers = (props) => {
  const { children } = props;
  const notistackRef = useRef();

  const onClickDismiss = (key) => () => {
    notistackRef.current.closeSnackbar(key);
  };

  return (
    <Suspense fallback={<div>Loading</div>}>
      <ThemeProvider theme={theme}>
        <ConfirmationProvider>
          <Elements stripe={stripePromise}>
            <SnackbarProvider
              ref={notistackRef}
              action={(key) => (
                <IconButton onClick={onClickDismiss(key)}>
                  <Close></Close>
                </IconButton>
              )}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              maxSnack={3}
            >
              <GoogleReCaptchaProvider reCaptchaKey='6Lef3MEUAAAAAEBgmgSa4i6a4Napq2iD32qS4DrG'>
                <AuthProvider strategy='finster'>{children}</AuthProvider>
              </GoogleReCaptchaProvider>
            </SnackbarProvider>
          </Elements>
        </ConfirmationProvider>
      </ThemeProvider>
    </Suspense>
  );
};

export default Providers;
