import { Box, Button, Grid, Hidden, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import laptop from '../../../assets/laptop-group-left.png';
import linesImg from '../../../assets/lines.png';
import i18n from '../../../i18n';
import NavBar, { NavBarDefaultProps, NavBarPropTypes } from '../../NavBar';
import DashboardDescription, {
  DashboardDescriptionDefaultProps,
  DashboardDescriptionPropTypes,
} from '../DashboardDescription';
import Image, { ImageDefaultProps, ImagePropTypes } from '../Image';
import { en_en } from './AppCard.lang.en-en';
import { nl_be } from './AppCard.lang.nl-be';
import { nl_nl } from './AppCard.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'AppCard', en_en);
i18n.addResourceBundle('nl-NL', 'AppCard', nl_nl);
i18n.addResourceBundle('nl-BE', 'AppCard', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  dashboardDescription: PropTypes.shape(DashboardDescriptionPropTypes),
  image: PropTypes.shape(ImagePropTypes),
  navbar: PropTypes.shape(NavBarPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  dashboardDescription: DashboardDescriptionDefaultProps,
  image: ImageDefaultProps,
  navbar: NavBarDefaultProps,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  container: {
    height: '100vh',
    position: 'relative',
    zIndex: theme.zIndex.drawer + 2,
    background: `url(${linesImg}) no-repeat, linear-gradient(180deg, #F9BB51 0%, #C75300 100%)`,
    backgroundSize: '100%',
    backgroundPosition: 'left bottom',
    overflow: 'hidden',
    [theme.breakpoints.down('sm')]: {
      height: 'auto',
      paddingBottom: theme.spacing(2),
    },
  },
  contentContainer: {
    // padding: `0 ${theme.spacing(20)}px`,
    alignItems: 'center',
    overflow: 'hidden',
  },
  appButton: {
    marginTop: theme.spacing(8),
    width: 242,
  },
  img: {
    [theme.breakpoints.up('xl')]: {
      marginTop: 65,
    },
  },
}));

/**
 * Displays the component
 */
const AppCard = (props) => {
  const { dashboardDescription, image, navBar } = props;
  const { t } = useTranslation('AppCard');
  const { appButton, container, contentContainer, img } = useStyles();
  const handleButtonClick = () => {
    console.log('App button clicked');
  };

  return (
    <Box className={container}>
      <NavBar {...navBar} variant='transparent'></NavBar>

      <Grid
        container
        className={clsx(contentContainer, 'homeContentContainer')}
      >
        <Grid item xs={12} lg={6}>
          <DashboardDescription
            {...dashboardDescription}
            inverted
            title={t('title')}
            subtitle={t('subtitle')}
            description={t('description')}
          >
            <Button
              className={appButton}
              color='secondary'
              variant='contained'
              onClick={handleButtonClick}
            >
              View the app
            </Button>
          </DashboardDescription>
        </Grid>
        <Hidden smDown>
          <Grid item lg={6}>
            <Image
              {...image}
              className={img}
              src={laptop}
              width='auto'
              maxHeight='90vh'
            ></Image>
          </Grid>
        </Hidden>
      </Grid>
    </Box>
  );
};

AppCard.propTypes = propTypes;
AppCard.defaultProps = defaultProps;

export default AppCard;
export { propTypes as AppCardPropTypes, defaultProps as AppCardDefaultProps };
