import { Box, Grid, makeStyles } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';
import React from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import i18n from '../../../i18n';
import Input from '../../Input';
import { en_en } from './CreditForm.lang.en-en';
import { nl_be } from './CreditForm.lang.nl-be';
import { nl_nl } from './CreditForm.lang.nl-nl';
import * as yup from 'yup';
import { useEffect } from 'react';

i18n.addResourceBundle('en-EN', 'CreditForm', en_en);
i18n.addResourceBundle('nl-NL', 'CreditForm', nl_nl);
i18n.addResourceBundle('nl-BE', 'CreditForm', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    creidts: PropTypes.number,
  }),
  onSubmit: PropTypes.func,
  error: PropTypes.string,
  disableButton: PropTypes.func,
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    credits: 100,
  },
  onSubmit: () => {},
  error: null,
  disableButton: () => {},
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
  },
}));

/**
 * Displays the component
 */
const CreditForm = React.forwardRef((props, ref) => {
  const { defaultValues, onSubmit, error, disableButton } = props;
  const { form } = useStyles();
  const { t } = useTranslation('CreditForm');

  const schema = yup.object().shape({
    credits: yup.string().required(t('required')),
  });

  const { register, formState, handleSubmit, errors } = useForm({
    defaultValues,
    validationSchema: schema,
  });

  useEffect(() => {
    if (!formState.isValid && formState.isSubmitted) {
      disableButton(true);
    } else {
      disableButton(false);
    }
  }, [formState]);

  return (
    <Box width={1}>
      <form ref={ref} className={form} onSubmit={handleSubmit(onSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Input
              inputRef={register}
              name='credits'
              type='number'
              fullWidth
              inputProps={{
                min: 1,
              }}
              inputNumber={{
                required: true,
                error: errors.credits?.message,
                label: t('credits'),
              }}
            />
          </Grid>
          {error && (
            <Grid item xs={12}>
              <Alert variant='filled' severity='error'>
                {error}
              </Alert>
            </Grid>
          )}
        </Grid>
      </form>
    </Box>
  );
});

CreditForm.propTypes = propTypes;
CreditForm.defaultProps = defaultProps;

export default CreditForm;
export {
  propTypes as CreditFormPropTypes,
  defaultProps as CreditFormDefaultProps,
};
