import { Box, makeStyles, Typography } from '@material-ui/core';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import pricingBg from '../../../assets/plans-bg.png';
import { useUpgrades } from '../../../hooks/useUpgrades';
import i18n from '../../../i18n';
import Plans, { PlansDefaultProps, PlansPropTypes } from '../Plans';
import { en_en } from './Pricing.lang.en-en';
import { nl_be } from './Pricing.lang.nl-be';
import { nl_nl } from './Pricing.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Pricing', en_en);
i18n.addResourceBundle('nl-NL', 'Pricing', nl_nl);
i18n.addResourceBundle('nl-BE', 'Pricing', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  id: PropTypes.string,
  plans: PropTypes.shape(PlansPropTypes),
};

/**
 * Defines the default props
 */
const defaultProps = {
  id: 'pricing',
  plans: PlansDefaultProps,
};

/**
 * Styles the component container
 */
const useStyles = makeStyles((theme) => ({
  contentContainer: {
    paddingBottom: theme.spacing(20),
    background: `transparent url(${pricingBg}) 0% 0% no-repeat padding-box; `,
    backgroundSize: '100%',
  },
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
  },
  title: {
    marginBottom: theme.spacing(4),
  },
  description: {
    maxWidth: 850,
    lineHeight: '2.5625rem',
    marginBottom: theme.spacing(9),
  },
}));

/**
 * Displays the component
 */
const Pricing = (props) => {
  const { id, plans } = props;
  const { contentContainer, container, description, title } = useStyles();
  const { upgrades, activeSubscription } = useUpgrades();

  const { t } = useTranslation('Pricing');
  return (
    <Box id={id} className={clsx(contentContainer, 'homeContentContainer')}>
      <Box className={container}>
        <Typography className={title} variant='h3'>
          {t('title', 'Pricing')}
        </Typography>
        <Typography className={description} variant='body1'>
          {t('description', 'Description')}
        </Typography>
      </Box>
      <Plans
        {...plans}
        plans={upgrades}
        activeSubscription={activeSubscription}
      ></Plans>
    </Box>
  );
};

Pricing.propTypes = propTypes;
Pricing.defaultProps = defaultProps;

export default Pricing;
export { propTypes as PricingPropTypes, defaultProps as PricingDefaultProps };
