import {
  Box,
  Button,
  Grid,
  makeStyles,
  Typography,
  Link,
} from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';
import React from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink } from 'react-router-dom';
import { usePattern } from '../../../hooks';
import i18n from '../../../i18n';
import Input from '../../Input';
import { en_en } from './RegisterForm.lang.en-en';
import { nl_be } from './RegisterForm.lang.nl-be';
import { nl_nl } from './RegisterForm.lang.nl-nl';
import * as yup from 'yup';

i18n.addResourceBundle('en-EN', 'RegisterForm', en_en);
i18n.addResourceBundle('nl-NL', 'RegisterForm', nl_nl);
i18n.addResourceBundle('nl-BE', 'RegisterForm', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    name: PropTypes.string,
    email: PropTypes.string,
    password: PropTypes.string,
    terms: PropTypes.bool,
  }),
  onSubmit: PropTypes.func,
  error: PropTypes.string,
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    name: 'test',
    email: 'test@test.com',
    password: 'test123',
    terms: false,
  },
  onSubmit: () => {},
  error: null,
};

/**
 * Styles the component
 */

/**
 * Displays the component
 */
const RegisterForm = (props) => {
  const { defaultValues, onSubmit, error } = props;
  const { t } = useTranslation('RegisterForm');

  const passwordPattern = usePattern('password');

  const schema = yup.object().shape({
    name: yup.string().required(t('name_required')),
    email: yup.string().required(t('email_required')).email(t('email_invalid')),
    password: yup
      .string()
      .required(t('password_required'))
      .min(6, t('password_min_length'))
      .matches(passwordPattern, t('password_composition')),
  });

  const { register, errors, reset, handleSubmit, formState } = useForm({
    defaultValues,
    validationSchema: schema,
  });

  const handleFormSubmit = (...args) => {
    reset(args[0]);
    onSubmit(...args);
  };
  return (
    <Box width={1}>
      <form onSubmit={handleSubmit(handleFormSubmit)}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Input
              fullWidth
              type='text'
              inputRef={register}
              inputText={{
                name: 'name',
                label: t('name'),
                error: errors.name?.message,
                required: true,
              }}
            />
          </Grid>

          <Grid item xs={12}>
            <Input
              fullWidth
              type='email'
              inputRef={register}
              inputEmail={{
                name: 'email',
                label: t('email'),
                error: errors.email?.message,
                required: true,
              }}
            />
          </Grid>

          <Grid item xs={12}>
            <Input
              fullWidth
              type='password'
              inputRef={register}
              inputPassword={{
                name: 'password',
                label: t('password'),
                error: errors.password?.message,
                required: true,
              }}
            />
          </Grid>

          <Grid item xs={12}>
            <Input
              type='checkbox'
              inputRef={register}
              inputCheckbox={{
                name: 'terms',
                label: t('terms'),
              }}
            />
          </Grid>

          {error && !formState.dirty && (
            <Grid item xs={12}>
              <Alert variant='filled' severity='error'>
                {error}
              </Alert>
            </Grid>
          )}
          <Grid item xs={12}>
            <Button
              fullWidth
              disabled={!formState.isValid && formState.isSubmitted}
              variant='contained'
              color='primary'
              type='submit'
            >
              {t('submit')}
            </Button>
          </Grid>
        </Grid>
      </form>
      <Grid container spacing={2} justify='space-between'>
        <Grid item xs>
          <Typography variant='body2'>
            <Link component={RouterLink} to={'/'}>
              {t('toHome', 'Back To Home')}
            </Link>
          </Typography>
        </Grid>
        <Grid item xs>
          <Typography variant='body2'>
            <Link component={RouterLink} to={'/login'}>
              {t('toRegister', 'Already have an account? Sign in')}
            </Link>
          </Typography>
        </Grid>
      </Grid>
    </Box>
  );
};

RegisterForm.propTypes = propTypes;
RegisterForm.defaultProps = defaultProps;

export default RegisterForm;
export {
  propTypes as RegisterFormPropTypes,
  defaultProps as RegisterFormDefaultProps,
};
