import PropTypes from 'prop-types';
import React from 'react';
import i18n from '../../../i18n';
import { en_en } from './Currency.lang.en-en';
import { nl_be } from './Currency.lang.nl-be';
import { nl_nl } from './Currency.lang.nl-nl';

i18n.addResourceBundle('en-EN', 'Currency', en_en);
i18n.addResourceBundle('nl-NL', 'Currency', nl_nl);
i18n.addResourceBundle('nl-BE', 'Currency', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  currency: PropTypes.oneOf(['eur']),
};

/**
 * Defines the default props
 */
const defaultProps = {
  currency: 'eur',
};

/**
 * Returns the corresponding currency symbol
 */
const getCurrency = (currency) => {
  switch (currency) {
    case 'eur':
      return '';
    default:
      return '';
  }
};

/**
 * Displays the component
 */
const Currency = (props) => {
  const { currency } = props;
  return <>{getCurrency(currency)}</>;
};

Currency.propTypes = propTypes;
Currency.defaultProps = defaultProps;

export default Currency;
export { propTypes as CurrencyPropTypes, defaultProps as CurrencyDefaultProps };
